import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ResultHandlerService } from '../../../../core/services/result.handler.service';
import { ToolService } from '../../services/tool.service';
import { ServiceResultDTO } from '../../../../core/interfaces/service-result/service-result.dto';
import { ToolDTO } from '../../../../shared/interfaces/tools/tool.dto';
import { UserTypeListComponent } from '../../../shared/user-type-list/user-type-list.component';
import { ToolUpdatePermissionsDTO } from '../../interfaces/tool-update-permissions.dto';
import { NavService } from 'src/app/core/services/nav.service';

@Component({
  selector: 'app-tool-edit-permission',
  templateUrl: './tool-edit-permission.component.html',
  styleUrls: ['./tool-edit-permission.component.scss']
})
export class ToolEditPermissionComponent implements OnInit {
  public tool!: ToolDTO;

  @ViewChild("userTypes")
  public userTypesComponent!: UserTypeListComponent;

  constructor(
    route: ActivatedRoute,
    private resultHandler: ResultHandlerService,
    private toolService: ToolService,
    navService: NavService
  ) {
    const data = route.snapshot.data["tool"] as ServiceResultDTO<ToolDTO>;
    if (data.isSuccess) {
      this.tool = data.value;
      navService.setToolNavMenu(this.tool);
    } else {
      this.resultHandler.handle(data);
    }
  }

  ngOnInit(): void {
  }

  public onClear = () => this.userTypesComponent.reset();

  public onSaveTool(): void {
    const tool: ToolUpdatePermissionsDTO = {
      permissionGroupsIds: this.userTypesComponent.getSelectedIds()
    };

    this.toolService.validateUpdatePermissions(this.tool.id, tool).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) this.update(tool);
      });
    }, (err) => {
      this.resultHandler.handle(err.error).then((success) => {
        if (success) this.update(tool);
      });
    });
  }

  public update(tool: ToolUpdatePermissionsDTO): void {
    this.toolService.updatePermissions(this.tool.id, tool).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) this.tool = data.value;
      });
    }, (err) => this.resultHandler.handle(err.error));
  }
}
