import { HttpClient, HttpParams } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { BaseAPIService } from '../../../core/api/base.api.service';
import { ServiceResultDTO } from '../../../core/interfaces/service-result/service-result.dto';
import { AdminApiEndpoints } from '../../constants/admin-api.constants';
import { RoleCreateDTO } from '../interfaces/role-create.dto';
import { RoleEditDTO } from '../interfaces/role-edit.dto';
import { RoleGroupedDTO } from '../interfaces/role-grouped.dto';
import { RoleListDTO } from '../interfaces/role-list.dto';
import { UserRoleDTO } from '../interfaces/user-role.dto';

@Injectable({
  providedIn: 'root'
})
export class RoleService extends BaseAPIService {

  constructor(
    @Inject("BASE_API_URL_WEB") baseApiUrl_Web: string,
    http: HttpClient
  ) {
    super(baseApiUrl_Web, http);
  }

  getRoles() {
    return this.http.get<ServiceResultDTO<RoleListDTO[]>>(this.constructRequestUrl(AdminApiEndpoints.GetRoles()));
  }

  getRolesGrouped(toolId?: number) {
    let params = new HttpParams()
      .set('toolId', toolId ?? '');

    return this.http.get<ServiceResultDTO<RoleGroupedDTO[]>>(
      this.constructRequestUrl(AdminApiEndpoints.GetRolesGrouped()), { params }
    );
  }

  getRole(id: number) {
    return this.http.get<ServiceResultDTO<RoleEditDTO>>(this.constructRequestUrl(AdminApiEndpoints.GetRoleById(id)));
  }

  getUsersRoles(toolId?: number) {
    let params = new HttpParams()
      .set('toolId', toolId ?? '');

    return this.http.get<ServiceResultDTO<UserRoleDTO[]>>(
      this.constructRequestUrl(AdminApiEndpoints.GetUsersRoles()), { params }
    );
  }

  createRole(model: RoleCreateDTO) {
    return this.http.post(this.constructRequestUrl(AdminApiEndpoints.CreateRole()), model);
  }

  updateRole(id: number, model: RoleEditDTO) {
    return this.http.put(this.constructRequestUrl(AdminApiEndpoints.UpdateRole(id)), model);
  }

  validateRemoveUserRole(userRole: UserRoleDTO) {
    return this.http.post<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(AdminApiEndpoints.ValidateRemoveUserRole()), userRole
    );
  }

  removeUserRole(userId: number, roleId: number) {
    return this.http.delete<ServiceResultDTO<boolean>>(this.constructRequestUrl(AdminApiEndpoints.RemoveUserRole(userId, roleId)));
  }
}
