import { HttpClient, HttpParams } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { BaseAPIService } from '../../../core/api/base.api.service';
import { ServiceResultDTO } from '../../../core/interfaces/service-result/service-result.dto';
import { AdminApiEndpoints } from '../../constants/admin-api.constants';
import { UserDTO } from '../interfaces/user.dto';
import { UserCreateDTO } from '../interfaces/user-create.dto';
import { UserListDTO } from '../interfaces/user-list.dto';
import { UserUpdateDTO } from '../interfaces/user-update.dto';
import { UserSecurityUpdateDTO } from '../interfaces/user-update-security.dto';
import { UserChangePasswordDTO } from '../interfaces/user-change-password.dto';

@Injectable({
  providedIn: 'root'
})
export class UserApiService extends BaseAPIService {

  constructor(
    @Inject("BASE_API_URL_WEB") baseApiUrl_Web: string, http: HttpClient
  ) {
    super(baseApiUrl_Web, http);
  }

  changePassword(model: UserChangePasswordDTO) {
    return this.http.post<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(AdminApiEndpoints.ChangePassword()), model
    );
  }

  getUsers(active: boolean, hasRoles?: boolean, toolId?: number) {
    let params = new HttpParams()
      .set('active', active)
      .set('hasRoles', hasRoles ?? '')
      .set('toolId', toolId ?? '');

    return this.http.get<ServiceResultDTO<UserListDTO[]>>(
      this.constructRequestUrl(AdminApiEndpoints.GetUsers()), { params }
    );
  }

  getUser(id: number) {
    return this.http.get<ServiceResultDTO<UserDTO>>(
      this.constructRequestUrl(AdminApiEndpoints.GetUserById(id))
    );
  }

  createUser(user: UserCreateDTO) {
    return this.http.post<ServiceResultDTO<UserDTO>>(
      this.constructRequestUrl(AdminApiEndpoints.CreateUser()), user
    );
  }

  updateUser(id: number, user: UserUpdateDTO) {
    return this.http.put<ServiceResultDTO<UserDTO>>(
      this.constructRequestUrl(AdminApiEndpoints.UpdateUser(id)), user
    );
  }

  updateUserSecurity(id: number, user: UserSecurityUpdateDTO) {
    return this.http.patch<ServiceResultDTO<UserDTO>>(
      this.constructRequestUrl(AdminApiEndpoints.UpdateUserSecurity(id)), user
    );
  }

  validateDelete(user: UserListDTO) {
    return this.http.post<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(AdminApiEndpoints.ValidateDeleteUser()), user
    );
  }

  deleteUser(id: number) {
    return this.http.delete<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(AdminApiEndpoints.DeleteUser(id))
    );
  }

  validateRestore(user: UserListDTO) {
    return this.http.post<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(AdminApiEndpoints.ValidateRestoreUser()), user
    );
  }

  restoreCarrier(id: number) {
    return this.http.post<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(AdminApiEndpoints.RestoreUser(id)), null
    );
  }

  validateUpdateRoles(userId: number, toolId: number, roles: number[]) {
    return this.http.post<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(AdminApiEndpoints.ValidateUpdateUserRolesByTool(userId, toolId)), roles
    );
  }

  updateRoles(userId: number, toolId: number, roles: number[]) {
    return this.http.patch<ServiceResultDTO<UserDTO>>(
      this.constructRequestUrl(AdminApiEndpoints.UpdateUserRolesByTool(userId, toolId)), roles
    );
  }
}
