import { ResourceLinkDTO } from './../interfaces/resource-link.dto';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { ResourceCenterDTO } from 'src/app/shared/interfaces/resource-center/resource-center.dto';
import { ServiceResultDTO } from '../../../core/interfaces/service-result/service-result.dto';
import { ResourceCenterApiService } from './resource-center.api.service';
import { ResourceLinkCreateDTO } from '../interfaces/resource-link-create.dto';

@Injectable({
  providedIn: 'root'
})
export class ResourceCenterService {

  constructor(private resourceCenterApiService: ResourceCenterApiService) { }

  createResourceLink(model?: ResourceLinkCreateDTO): Observable<ServiceResultDTO<ResourceLinkDTO>> {
    return this.resourceCenterApiService.createResourceLink(model);
  }

  deleteResourceLink(id: number): Observable<ServiceResultDTO<boolean>> {
    return this.resourceCenterApiService.deleteResourceLink(id);
  }

  getResourceLinkById(id: number): Observable<ServiceResultDTO<ResourceLinkDTO>> {
    return this.resourceCenterApiService.getResourceLinkById(id);
  }

  getResourceLinks(toolId: number, regionId?: number): Observable<ServiceResultDTO<ResourceLinkDTO[]>> {
    return this.resourceCenterApiService.getResourceLinks(toolId, regionId);
  }

  setResourceCenter(model?: ResourceCenterDTO): Observable<ServiceResultDTO<ResourceCenterDTO>> {
    return this.resourceCenterApiService.setResourceCenter(model);
  }

  updateResourceLink(id: number, model?: ResourceLinkCreateDTO): Observable<ServiceResultDTO<ResourceLinkDTO>> {
    return this.resourceCenterApiService.updateResourceLink(id, model);
  }

  updateResourceLinkOrder(id: number, newOrder: number): Observable<ServiceResultDTO<boolean>> {
    return this.resourceCenterApiService.updateResourceLinkOrder(id, newOrder);
  }

  validateDeleteResourceLink(model: ResourceLinkDTO): Observable<ServiceResultDTO<boolean>> {
    return this.resourceCenterApiService.validateDeleteResourceLink(model);
  }
}
