import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { InputValidatorService } from '../../../../shared/services/input-validator.service';
import { ResultHandlerService } from '../../../../core/services/result.handler.service';
import { ToolService } from '../../services/tool.service';
import { ServiceResultDTO } from '../../../../core/interfaces/service-result/service-result.dto';
import { ToolDTO } from '../../../../shared/interfaces/tools/tool.dto';
import { ToolUpdateDTO } from '../../interfaces/tool-update.dto';
import { SingleUploadComponent } from '../../../../shared/components/single-upload/single-upload.component';
import { NavService } from 'src/app/core/services/nav.service';
import { MaxFileSizes } from 'src/app/shared/constants/file-sizes';

@Component({
  selector: 'app-tool-edit-general-info',
  templateUrl: './tool-edit-general-info.component.html',
  styleUrls: ['./tool-edit-general-info.component.scss']
})
export class ToolEditGeneralInfoComponent implements OnInit {
  MaxFileSizes = MaxFileSizes;

  public form!: FormGroup;
  public tool!: ToolDTO;

  @ViewChild("upload")
  public upload!: SingleUploadComponent;
  @ViewChild("uploadFullSizeLogo")
  public uploadFullSizeLogo!: SingleUploadComponent;

  constructor(
    route: ActivatedRoute,
    public inputValidatorService: InputValidatorService,
    private fb: FormBuilder,
    private resultHandler: ResultHandlerService,
    private toolService: ToolService,
    navService: NavService
  ) {
    const data = route.snapshot.data["tool"] as ServiceResultDTO<ToolDTO>;
    if (data.isSuccess) {
      this.tool = data.value;
      navService.setToolNavMenu(this.tool);
    } else {
      this.resultHandler.handle(data);
    }
  }

  ngOnInit(): void {
    this.initializeForm();
  }

  initializeForm() {
    this.form = this.fb.group({
      name: [this.tool != null ? this.tool.name : '', Validators.required],
      description: [this.tool != null ? this.tool.description : ''],
      isFavoritedByDefault: [this.tool != null ? this.tool.isFavoritedByDefault : false]
    });
  }

  public onSaveTool(): void {
    this.form.markAllAsTouched();
    if (!this.form.valid) {
      return;
    }

    const tool = this.form.getRawValue() as ToolUpdateDTO;
    const primaryLogo = this.upload.getFile();
    const fullSizeLogo = this.uploadFullSizeLogo.getFile();

    this.toolService.validateUpdateGeneralInfo(this.tool.id, tool, primaryLogo).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) this.update(tool, primaryLogo, fullSizeLogo);
      });
    }, (err) => {
      this.resultHandler.handle(err.error).then((success) => {
        if (success) this.update(tool, primaryLogo, fullSizeLogo);
      });
    });
  }

  public onUndo(): void {
    this.initializeForm();
    this.upload.reset();
    this.uploadFullSizeLogo.reset();
  }

  public update(tool: ToolUpdateDTO, primaryLogo?: File, fullSizeLogo?: File): void {
    this.toolService.updateGeneralInfo(this.tool.id, tool, primaryLogo, fullSizeLogo).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.tool = data.value;
          this.upload.reloadCurrentFile(primaryLogo, this.tool.primaryLogoFilePath);
          this.uploadFullSizeLogo.reloadCurrentFile(fullSizeLogo, this.tool.fullSizeLogoFilePath);

          this.onUndo();
        }
      });
    }, (err) => this.resultHandler.handle(err.error));
  }
}
