import { Injectable } from '@angular/core';
import { Resolve, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs';
import { CountryService } from '../services/country.service';
import { CountryDTO } from '../interfaces/country.dto';
import { ServiceResultDTO } from '../../../core/interfaces/service-result/service-result.dto';

@Injectable()
export class CountryListResolver implements Resolve<ServiceResultDTO<CountryDTO[]>> {

  constructor(private countryService: CountryService) {
  }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<ServiceResultDTO<CountryDTO[]>> {
    return this.countryService.get();
  }
}
