import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable, Inject } from '@angular/core';
import { BaseAPIService } from '../../../core/api/base.api.service';
import { CarrierDTO } from '../interfaces/carrier.dto';
import { CarrierCreateDTO } from '../interfaces/carrier-create.dto';
import { CarrierUpdateDTO } from '../interfaces/carrier-update.dto';
import { ServiceResultDTO } from '../../../core/interfaces/service-result/service-result.dto';
import { AdminApiEndpoints } from '../../constants/admin-api.constants';
import { toFormData } from '../../../shared/utilities/shared';

@Injectable({
  providedIn: "root"
})
export class CarrierApiService extends BaseAPIService {
  constructor(
    @Inject("BASE_API_URL_WEB") baseApiUrl_Web: string,
    http: HttpClient
  ) {
    super(baseApiUrl_Web, http);
  }

  public createCarrier(carrier: CarrierCreateDTO, file?: File) {
    const formData = toFormData(carrier);
    if (file != null) formData.append("file", file);

    return this.http.post<ServiceResultDTO<CarrierDTO>>(
      this.constructRequestUrl(AdminApiEndpoints.CreateCarrier()), formData
    );
  }

  public getCarriers(active: boolean) {
    let params = new HttpParams()
      .set('active', active);

    return this.http.get<ServiceResultDTO<CarrierDTO[]>>(
      this.constructRequestUrl(AdminApiEndpoints.GetCarriers()), { params }
    );
  }

  public getCarrier(id: number) {
    return this.http.get<ServiceResultDTO<CarrierDTO>>(
      this.constructRequestUrl(AdminApiEndpoints.GetCarrierById(id))
    );
  }

  public validateUpdate(id: number, carrier: CarrierUpdateDTO, file?: File) {
    const formData = toFormData(carrier);
    if (file != null) formData.append("file", file);

    return this.http.post<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(AdminApiEndpoints.ValidateUpdateCarrier(id)), formData
    );
  }

  public updateCarrier(id: number, carrier: CarrierUpdateDTO, file?: File) {
    const formData = toFormData(carrier);
    if (file != null) formData.append("file", file);

    return this.http.put<ServiceResultDTO<CarrierDTO>>(
      this.constructRequestUrl(AdminApiEndpoints.UpdateCarrier(id)), formData
    );
  }

  public validateDelete(carrier: CarrierDTO) {
    return this.http.post<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(AdminApiEndpoints.ValidateDeleteCarrier()), carrier
    );
  }

  public delete(id: number) {
    return this.http.delete<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(AdminApiEndpoints.DeleteCarrier(id))
    );
  }

  public validateRestore(carrier: CarrierDTO) {
    return this.http.post<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(AdminApiEndpoints.ValidateRestoreCarrier()), carrier
    );
  }

  public restoreCarrier(id: number) {
    return this.http.post<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(AdminApiEndpoints.RestoreCarrier(id)), null
    );
  }
}
