import { ResourceLinkDTO } from './../interfaces/resource-link.dto';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { ResourceCenterDTO } from 'src/app/shared/interfaces/resource-center/resource-center.dto';
import { BaseAPIService } from '../../../core/api/base.api.service';
import { ServiceResultDTO } from '../../../core/interfaces/service-result/service-result.dto';
import { AdminApiEndpoints } from '../../constants/admin-api.constants';
import { ResourceLinkCreateDTO } from '../interfaces/resource-link-create.dto';

@Injectable({
  providedIn: 'root'
})
export class ResourceCenterApiService extends BaseAPIService {

  constructor(
    @Inject("BASE_API_URL_WEB") baseApiUrl_Web: string, http: HttpClient
  ) {
    super(baseApiUrl_Web, http);
  }

  createResourceLink(model?: ResourceLinkCreateDTO) {
    const formData = new FormData();
    formData.append('modelJsonString', JSON.stringify(model));

    if (model != null && model.file != null) formData.append("files", model.file);

    return this.http.post<ServiceResultDTO<ResourceLinkDTO>>(
      this.constructRequestUrl(AdminApiEndpoints.CreateResourceLink()), formData
    )
  }

  deleteResourceLink(id: number) {
    return this.http.delete<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(AdminApiEndpoints.DeleteResourceLink(id))
    )
  }

  getResourceLinkById(id: number) {
    return this.http.get<ServiceResultDTO<ResourceLinkDTO>>(
      this.constructRequestUrl(AdminApiEndpoints.GetResourceLinkById(id))
    );
  }

  getResourceLinks(toolId: number, regionId?: number) {
    let params = new HttpParams()
      .set('regionId', regionId ?? '')

    return this.http.get<ServiceResultDTO<ResourceLinkDTO[]>>(
      this.constructRequestUrl(AdminApiEndpoints.GetResourceLinks(toolId)), { params }
    );
  }

  setResourceCenter(model?: ResourceCenterDTO) {
    return this.http.put<ServiceResultDTO<ResourceCenterDTO>>(
      this.constructRequestUrl(AdminApiEndpoints.SetResourceCenter()), model
    );
  }

  updateResourceLink(id: number, model?: ResourceLinkCreateDTO) {
    const formData = new FormData();
    formData.append('modelJsonString', JSON.stringify(model));

    if (model != null && model.file != null) formData.append("files", model.file);

    return this.http.put<ServiceResultDTO<ResourceLinkDTO>>(
      this.constructRequestUrl(AdminApiEndpoints.UpdateResourceLink(id)), formData
    )
  }

  updateResourceLinkOrder(id: number, newOrder: number) {
    var resourceLink = { order: newOrder };
    return this.http.put<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(AdminApiEndpoints.UpdateResourceLinkOrder(id)), resourceLink
    );
  }

  validateDeleteResourceLink(model?: ResourceLinkDTO) {
    return this.http.post<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(AdminApiEndpoints.ValidateDeleteResourceLink()), model
    );
  }
}
