import { HttpClient } from '@angular/common/http';
import { Component, Inject, OnInit } from '@angular/core';
import { AccountService } from '../../../../authentication/services/account.service';
import { BaseAPIService } from '../../../../core/api/base.api.service';
import { AdminApiEndpoints } from '../../../constants/admin-api.constants';

@Component({
  selector: 'app-file-manager',
  templateUrl: './file-manager.component.html'
})
export class FileManagerComponent extends BaseAPIService implements OnInit {
  public ajaxSettings!: object;
  public allowDragAndDrop: boolean = true;

  constructor(
    @Inject("BASE_API_URL_WEB") private baseApiUrl_Web: string,
    http: HttpClient,
    private accountService: AccountService
  ) {
    super(baseApiUrl_Web, http);
  }

  public ngOnInit(): void {
    this.ajaxSettings = {
      url: this.constructRequestUrl(AdminApiEndpoints.FileOperations()),
      downloadUrl: this.constructRequestUrl(AdminApiEndpoints.Download()),
      uploadUrl: this.constructRequestUrl(AdminApiEndpoints.Upload()),
      getImageUrl: this.constructRequestUrl(AdminApiEndpoints.GetImage())
    };
  }

  beforeSend(args: any) {
    let token = this.accountService.getCurrentUserAuthToken();
    args.ajaxSettings.beforeSend = function (args: { httpRequest: { setRequestHeader: (arg0: string, arg1: string) => void; }; }) {
      //Setting authorization header 
      args.httpRequest.setRequestHeader("Authorization", "Bearer " + token)
    }
  }
}
