import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { ServiceResultDTO } from '../../../../core/interfaces/service-result/service-result.dto';
import { InputValidatorService } from '../../../../shared/services/input-validator.service';
import { ResultHandlerService } from '../../../../core/services/result.handler.service';
import { RoleService } from '../../services/role.service';
import { UserService } from '../../../user-management/services/user.service';
import { RoleGroupedDTO } from '../../interfaces/role-grouped.dto';
import { UserRoleDTO } from '../../interfaces/user-role.dto';
import { UserDTO } from '../../../user-management/interfaces/user.dto';
import { UserListDTO } from '../../../user-management/interfaces/user-list.dto';
import { formatAdminToolName } from '../../../../shared/utilities/shared';
import { SharedService } from 'src/app/shared/services/shared.service';
import { NavService } from 'src/app/core/services/nav.service';

@Component({
  selector: 'app-role-tools-user',
  templateUrl: './role-tools-user.component.html',
  styleUrls: ['./role-tools-user.component.scss']
})
export class RoleToolsUserComponent implements OnInit {
  formatAdminToolName = formatAdminToolName;

  public userRoles!: UserRoleDTO[];
  public roleGroup?: RoleGroupedDTO;
  public user?: UserDTO;

  public assignUser: boolean;
  public assignedUserId?: number;

  public usersAvailable!: UserListDTO[];
  public fields: Object = { text: 'name', value: 'id' };

  private toolId: number;

  constructor(
    route: ActivatedRoute,
    private roleService: RoleService,
    private resultHandler: ResultHandlerService,
    private userService: UserService,
    public inputValidatorService: InputValidatorService,
    private location: Location,
    private sharedService: SharedService,
    private navService: NavService
  ) {
    const userData = route.snapshot.data["user"] as ServiceResultDTO<UserDTO>;
    this.assignUser = userData == null;
    if (userData != null) {
      this.handleUser(userData);
    }

    this.toolId = Number(route.snapshot.params.toolId);
    this.roleService.getRolesGrouped(this.toolId).subscribe((data) => {
      this.handleRoles(data);
    });
  }

  ngOnInit(): void {
    this.userService.getUsers(true, false, this.toolId).subscribe(data => {
      if (data.isSuccess) {
        this.usersAvailable = data.value;
      } else {
        this.resultHandler.handle(data);
      }
    });
    this.navService.setRoleManagementMenu(this.sharedService);
  }

  handleUser(data: ServiceResultDTO<UserDTO>) {
    if (data.isSuccess) {
      this.user = data.value;
    } else {
      this.resultHandler.handle(data);
    }
  }

  handleRoles(rolesGrouped: ServiceResultDTO<RoleGroupedDTO[]>): void {
    if (rolesGrouped.isSuccess) {
      this.roleGroup = rolesGrouped.value.length ? rolesGrouped.value[0] : undefined;
    } else {
      this.resultHandler.handle(rolesGrouped);
    }
  }

  onUserChange(e: any) {
    if (e.itemData != null && e.itemData.id != null) {
      this.userService.getUser(e.itemData.id).subscribe(data => {
        this.handleUser(data);
      })
    } else {
      this.user = null!;
    }
  }

  onUndoClicked(e: any) {
    this.assignedUserId = undefined;
  }

  onUserSave = (e: any) => this.location.back();
}
