import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { FormGroup } from '@angular/forms';
import { PageSettingsModel } from '@syncfusion/ej2-angular-grids';
import { ToastUtility } from '@syncfusion/ej2-notifications';
import { LogDetailDTO } from '../../interfaces/log-detail.dto';
import { ServiceResultDTO } from '../../../../core/interfaces/service-result/service-result.dto';
import { LogsService } from '../../services/logs.service';
import { getLogsFormGroup } from '../../forms/logs-form-group';
import { DateDisplayFunctions } from '../../../../shared/utilities/shared';

@Component({
  selector: 'app-logs-list',
  templateUrl: './logs-list.component.html'
})
export class LogsListComponent implements OnInit {
  public logs!: any[];
  public pageSettings!: PageSettingsModel;
  public day: number = new Date().getDate();
  public month: number = new Date().getMonth();
  public fullYear: number = new Date().getFullYear();
  public maxDate: Date = new Date(this.fullYear, this.month, this.day, 23, 59, 59);
  public today: Date = new Date(this.fullYear, this.month, this.day, 0, 0, 0);
  public filter!: Object;
  public filterSettings!: Object;

  public form: FormGroup = getLogsFormGroup({
    endDate: this.maxDate,
    startDate: this.today,
    filterPattern: ''
  });

  constructor(
    route: ActivatedRoute,
    private logsService: LogsService
  ) {
    var data = route.snapshot.data["logs"] as ServiceResultDTO<LogDetailDTO[]>;
    if (data.isSuccess) {
      this.logs = this.convertLogs(data.value);
    }
  }

  ngOnInit(): void {
    this.pageSettings = { pageSize: 10 };
    this.filterSettings = { type: "Menu" };  
    this.filter = { type: "CheckBox" };
  }

  convertLogs(logs: LogDetailDTO[]): any {
    return logs.map(function (d) {
      return {
        timestamp: DateDisplayFunctions.createDateTimeDisplayValue(d.timestamp),
        message: d.message,
        level: d.level,
        json: JSON.parse(d.json)
      }
    })
  }

  onSubmit(): void {
    if (!this.form.valid) {
      ToastUtility.show({
        content: 'Form is invalid!',
        position: { X: 'Right', Y: 'Top' },
        cssClass: 'e-toast-danger'
      });

      return;
    }

    const formValues = this.form.getRawValue();

    this.logsService.getWithFilters({
      startDate: formValues.startDate,
      endDate: formValues.endDate,
      filterPattern: formValues.filterPattern
    }).subscribe((data) => {
      if (data.isSuccess) {
        this.logs = this.convertLogs(data.value);
      } else {
        ToastUtility.show({
          content: 'Error retriving the logs!',
          position: { X: 'Right', Y: 'Top' },
          cssClass: 'e-toast-danger'
        });
      }
    }, (err) => {
      ToastUtility.show({
        content: err.error.errors.join('\n'),
        position: { X: 'Right', Y: 'Top' },
        cssClass: 'e-toast-danger'
      });
    });
  }
}
