import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { BaseGridComponent } from '../../../../shared/components/base-grid/base-grid.component';
import { ResultHandlerService } from '../../../../core/services/result.handler.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { RoleService } from '../../services/role.service';
import { ServiceResultDTO } from '../../../../core/interfaces/service-result/service-result.dto';
import { ToolDTO } from '../../../../shared/interfaces/tools/tool.dto';
import { UserRoleDTO } from '../../interfaces/user-role.dto';
import { formatAdminToolName } from '../../../../shared/utilities/shared';
import { ApplicationUrls } from 'src/app/shared/constants/application-urls';
import { NavService } from 'src/app/core/services/nav.service';
import { UrlUtils } from 'src/app/shared/utilities/url-utils';

@Component({
  selector: 'app-role-tools-list',
  templateUrl: './role-tools-list.component.html',
  styleUrls: ['./role-tools-list.component.scss']
})
export class RoleToolsListComponent extends BaseGridComponent implements OnInit {
  replaceParams = UrlUtils.replaceParams;
  formatAdminToolName = formatAdminToolName;

  public tool!: ToolDTO;
  public userRoles!: UserRoleDTO[];

  public hasRegion: boolean = false;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private resultHandler: ResultHandlerService,
    private sharedService: SharedService,
    private roleService: RoleService,
    private navService: NavService
  ) {
    super();

    this.handleDataFromRoute();

    route.params.subscribe(val => {
      this.handleDataFromRoute();
      this.handleTool();
    });
  }

  ngOnInit(): void {
    this.handleTool();
    this.filterSettings = { type: 'CheckBox' };
    this.searchSettings = { fields: ['appUser.name'] };
  }

  handleDataFromRoute() {
    const data = this.route.snapshot.data["roles"] as ServiceResultDTO<UserRoleDTO[]>;
    this.handleRoles(data);
    this.setupPagination(data.value);
    this.navService.setRoleManagementMenu(this.sharedService);
  }

  handleRoles(data: ServiceResultDTO<UserRoleDTO[]>) {
    if (data.isSuccess) {
      this.userRoles = data.value;
      this.hasRegion = data.value.filter(r => r.appRole.region != null).length > 0;
    } else {
      this.resultHandler.handle(data);
    }
  }

  handleTool() {
    const id = Number(this.route.snapshot.paramMap.get('toolId'));
    this.sharedService.getTool(id).subscribe((data) => {
      if (data.isSuccess) {
        this.tool = data.value;
      } else {
        this.resultHandler.handle(data);
      }
    });
  }

  loadUsersRoles(): void {
    this.roleService.getUsersRoles(this.tool.id).subscribe((data) => {
      this.handleRoles(data);
    }, (err) => this.resultHandler.handle(err.error));
  }

  onAssignUser() {
    const path = this.replaceParams(
      ApplicationUrls.GlobalToolSuite.RoleManagement.RoleToolManagement.RoleToolUserManagement.AssignUser.path,
      this.tool.id
    );
    this.router.navigate([path], { state: history.state });
  }

  onEditRole(userRole: UserRoleDTO) {
    const path = this.replaceParams(
      ApplicationUrls.GlobalToolSuite.RoleManagement.RoleToolManagement.RoleToolUserManagement.EditUser.path,
      this.tool.id,
      userRole.appUser.id
    );

    this.router.navigate([path], { state: history.state });
  }

  onRemoveRole(userRole: UserRoleDTO) {
    this.roleService.validateRemoveUserRole(userRole).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.deleteUserRole(userRole);
        }
      });
    }, (err) => {
      this.resultHandler.handle(err.error).then((success) => {
        if (success) {
          this.deleteUserRole(userRole);
        }
      });
    });
  }

  deleteUserRole(userRole: UserRoleDTO): void {
    this.roleService.removeUserRole(userRole.appUser.id, userRole.appRole.id).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.loadUsersRoles();
        }
      });
    }, (err) => {
      this.resultHandler.handle(err.error);
    });
  }
}
