import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { LogsDataApiService } from './logs.api.service';
import { LogDetailDTO } from '../interfaces/log-detail.dto';
import { LogFilterDTO } from '../interfaces/log-filter.dto';
import { ServiceResultDTO } from '../../../core/interfaces/service-result/service-result.dto';

@Injectable()
export class LogsService {
  constructor(private logsDataApiService: LogsDataApiService) {
  }

  public get(): Observable<ServiceResultDTO<LogDetailDTO[]>> {
    return this.logsDataApiService.get();
  }

  public getWithFilters(filter: LogFilterDTO): Observable<ServiceResultDTO<LogDetailDTO[]>> {
    return this.logsDataApiService.getWithFilters(filter);
  }
}
