import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Location } from '@angular/common';
import { InputValidatorService } from '../../../../shared/services/input-validator.service';
import { UserService } from '../../services/user.service';
import { ResultHandlerService } from '../../../../core/services/result.handler.service';
import { RoleService } from '../../../roles/services/role.service';
import { UserCreateDTO } from '../../../user-management/interfaces/user-create.dto';
import { RoleGroupedDTO } from '../../../roles/interfaces/role-grouped.dto';
import { ServiceResultDTO } from '../../../../core/interfaces/service-result/service-result.dto';
import { ToolsList } from '../../../../shared/constants/tools-list.enum';
import { formatAdminToolName } from '../../../../shared/utilities/shared';

@Component({
  selector: 'app-user-create',
  templateUrl: './user-create.component.html',
  styleUrls: ['./user-create.component.scss']
})
export class UserCreateComponent implements OnInit {
  userCreateForm!: FormGroup;
  public roles!: RoleGroupedDTO[];
  public globalAdminRoles!: any[];
  public rolesTree!: Object;
  public checkedRoles: { group: string, id: number }[] = [];

  constructor(
    private fb: FormBuilder,
    private userService: UserService,
    private location: Location,
    private resultHandler: ResultHandlerService,
    public inputValidatorService: InputValidatorService,
    private roleService: RoleService
  ) { }

  ngOnInit(): void {
    this.initializeForm();
    this.roleService.getRolesGrouped().subscribe((data) => {
      this.handleRoles(data);
    });
  }

  handleRoles(roles: ServiceResultDTO<RoleGroupedDTO[]>): void {
    if (roles.isSuccess) {
      this.roles = roles.value;
      this.setupRolesList();
    } else {
      this.resultHandler.handle(roles);
    }
  }

  initializeForm() {
    this.userCreateForm = this.fb.group({
      firstName: ['', Validators.required],
      lastName: ['', Validators.required],
      email: ['', [Validators.required, Validators.email]]
    });
  }

  return = () => this.location.back();

  clearForm() {
    this.userCreateForm.reset();
    this.setupRolesList();
  }

  setupRolesList() {
    const rolesFormated = this.getRolesFormated(this.roles);
    this.rolesTree = { dataSource: rolesFormated, id: 'id', text: 'name', child: 'subChild' }

    this.globalAdminRoles = this.roles.filter(r => r.tool.id == ToolsList.GlobalToolSuite)[0].tool.groups.map(group => {
      return {
        name: group.name,
        subChild: group.roles.map(gr => {
          return {
            id: gr.id,
            group: gr.id,
            name: gr.name
          }
        })
      }
    })
  }

  getRolesFormated(roles: RoleGroupedDTO[]) {
    return roles.filter(r => r.tool.id != ToolsList.GlobalToolSuite).map(role => {
      return {
        id: role.tool.id,
        name: formatAdminToolName(role.tool),
        subChild: role.tool.regions.map(region => {
          return {
            id: `${role.tool.id}-${region.id}`,
            name: region.name,
            subChild: region.roles.map(rr => {
              return {
                id: rr.id,
                name: rr.name,
                group: `${role.tool.id}.${region.id}`,
                type: 'radio'
              }
            })
          }
        }).concat(role.tool.groups.map(group => {
          return {
            id: group.name,
            name: group.name,
            subChild: group.roles.map(gr => {
              return {
                id: gr.id,
                name: gr.name,
                group: `${gr.id}`,
                type: 'checkbox'
              }
            })
          }
        }))
      }
    });
  }

  roleChecked(data: any) {
    var index = this.checkedRoles.findIndex(role => role.group == data.group);
    if (index != -1) {
      this.checkedRoles[index].id = data.id;
    } else {
      this.checkedRoles.push({ group: data.group, id: data.id });
    }
  }

  public onSaveUser(): void {
    this.userCreateForm.markAllAsTouched();
    if (!this.userCreateForm.valid) {
      return;
    }

    let model = this.userCreateForm.value as UserCreateDTO;
    //always creating an admin user
    model.userType = 1;
    model.roleIds = this.checkedRoles.map(role => role.id);

    this.userService.createUser(model).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.return();
        }
      });
    }, (err) => {
      this.resultHandler.handle(err.error);
    });
  }
}
