import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { BaseGridComponent } from '../../../../shared/components/base-grid/base-grid.component';
import { CarrierDTO } from '../../interfaces/carrier.dto';
import { ServiceResultDTO } from '../../../../core/interfaces/service-result/service-result.dto';
import { ResultHandlerService } from '../../../../core/services/result.handler.service';
import { CarrierService } from '../../services/carrier.service';
import { ApplicationUrls } from 'src/app/shared/constants/application-urls';
import { UrlUtils } from 'src/app/shared/utilities/url-utils';
import { getFilePathEncoded } from 'src/app/shared/utilities/shared';

@Component({
  selector: 'app-carrier-list',
  templateUrl: './carrier-list.component.html',
  styleUrls: ['./carrier-list.component.scss']
})
export class CarrierListComponent extends BaseGridComponent implements OnInit {
  replaceParams = UrlUtils.replaceParams;
  createCarrierUrl = ApplicationUrls.GlobalToolSuite.CarrierManagement.NewCarrier.path;
  getFilePathEncoded = getFilePathEncoded;

  public carriers!: CarrierDTO[];
  private regionsFilter!: any[];

  constructor(
    route: ActivatedRoute,
    private router: Router,
    private resultHandler: ResultHandlerService,
    private carrierService: CarrierService
  ) {
    super();

    var data = route.snapshot.data["carriers"] as ServiceResultDTO<CarrierDTO[]>;
    this.handleCarriers(data);
  }

  ngOnInit(): void {
    this.filterSettings = { type: 'CheckBox' };
    this.searchSettings = { fields: ['name'] };
  }

  handleCarriers(data: ServiceResultDTO<CarrierDTO[]>): void {
    if (data.isSuccess) {
      this.carriers = data.value;
      this.regionsFilter = this.getRegionsFilter();
      this.setupPagination(this.carriers);
    } else {
      this.resultHandler.handle(data);
    }
  }

  loadCarriers(): void {
    this.carrierService.getCarriers(!this.showArchived).subscribe((data) => {
      this.handleCarriers(data);
    }, (err) => this.resultHandler.handle(err.error));
  }

  onArchiveChange(showArchived: boolean): void {
    this.showArchived = showArchived;
    this.loadCarriers();
  }

  onEditCarrier(carrier: CarrierDTO): void {
    const path = this.replaceParams(
      ApplicationUrls.GlobalToolSuite.CarrierManagement.EditCarrier.path,
      carrier.id
    );
    this.router.navigate([path]);
  }

  onRestoreCarrier(carrier: CarrierDTO): void {
    this.carrierService.validateRestore(carrier).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.restoreCarrier(carrier);
        }
      });
    }, (err) => {
      this.resultHandler.handle(err.error).then((success) => {
        if (success) {
          this.restoreCarrier(carrier);
        }
      });
    });
  }

  restoreCarrier(carrier: CarrierDTO): void {
    this.carrierService.restoreCarrier(carrier).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.loadCarriers();
        }
      });
    }, (err) => {
      this.resultHandler.handle(err.error);
    });
  }

  onDeleteCarrier(carrier: CarrierDTO): void {
    this.carrierService.validateDelete(carrier).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.deleteCarrier(carrier);
        }
      });
    }, (err) => {
      this.resultHandler.handle(err.error).then((success) => {
        if (success) {
          this.deleteCarrier(carrier);
        }
      });
    });
  }

  deleteCarrier(carrier: CarrierDTO): void {
    this.carrierService.delete(carrier).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.loadCarriers();
        }
      });
    }, (err) => {
      this.resultHandler.handle(err.error);
    });
  }

  actionBegin(e: any) {
    switch (e.requestType) {
      case 'filtering':
        e.columns.forEach((col: { field: string; operator: string; }) => {
          if (col.field === "regionsDisplay" && col.operator === 'equal') {
            col.operator = "contains";
          }
        })
        break;
      case 'filterbeforeopen':
        if (e.columnName === "regionsDisplay") {
          e.filterModel.options.dataSource = this.regionsFilter;
        }
        break;
    }
  }

  getRegionsFilter(): any[] {
    var regions = this.carriers.flatMap(r => r.regionsDisplay.split(', '))
      .filter((v, i, a) => a.indexOf(v) === i);
    return regions.map((r) => { return { regionsDisplay: r } });
  }
}
