import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable, Inject } from '@angular/core';
import { BaseAPIService } from '../../../core/api/base.api.service';
import { LogFilterDTO } from '../interfaces/log-filter.dto';
import { LogDetailDTO } from '../interfaces/log-detail.dto';
import { ServiceResultDTO } from '../../../core/interfaces/service-result/service-result.dto';
import { AdminApiEndpoints } from '../../constants/admin-api.constants';

@Injectable({
  providedIn: "root"
})
export class LogsDataApiService extends BaseAPIService {
  constructor(
    @Inject("BASE_API_URL_WEB") baseApiUrl_Web: string,
    http: HttpClient
  ) {
    super(baseApiUrl_Web, http);
  }

  public get() {
    return this.http.get<ServiceResultDTO<LogDetailDTO[]>>(
      this.constructRequestUrl(AdminApiEndpoints.GetLogs())
    );
  }

  public getWithFilters(filter: LogFilterDTO) {
    let params = new HttpParams()
      .set('startDate', filter.startDate.toJSON())
      .set('endDate', filter.endDate.toJSON())
      .set('filterPattern', filter.filterPattern);

    return this.http.get<ServiceResultDTO<LogDetailDTO[]>>(
      this.constructRequestUrl(AdminApiEndpoints.GetLogs()), { params }
    );
  }
}
