import { BehaviorSubject } from 'rxjs';
import { Component, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { InputValidatorService } from 'src/app/shared/services/input-validator.service';
import { RegionDTO } from 'src/app/admin/region-management/interfaces/region.dto';
import { SingleUploadComponent } from 'src/app/shared/components/single-upload/single-upload.component';
import { ResourceLinkCreateDTO } from '../../interfaces/resource-link-create.dto';
import { CustomValidators } from 'src/app/shared/utilities/custom-validators';
import { ResourceCenterService } from '../../services/resource-center.service';
import { ResultHandlerService } from 'src/app/core/services/result.handler.service';

@Component({
  selector: 'app-resource-link',
  templateUrl: './resource-link.component.html',
  styleUrls: ['./resource-link.component.scss']
})
export class ResourceLinkComponent implements OnInit {
  form!: FormGroup;
  public types: string[] = ['Link','Video','PDF'];
  public fields: Object = { text: 'name', value: 'id' };
  public extensionsAllowed!: string;
  public resourceLink: ResourceLinkCreateDTO | null = null;

  @ViewChild("upload")
  public upload!: SingleUploadComponent;

  @Input()
  public title!: string;

  @Input()
  public regions!: RegionDTO[];

  private _resourceLinkSubject = new BehaviorSubject<ResourceLinkCreateDTO | null>(null);
  public resourceLink$ = this._resourceLinkSubject.asObservable();

  @Input()
  public toolId!: number;

  @Output()
  onSaveResourceLink = new EventEmitter<ResourceLinkCreateDTO>();

  @Output()
  onClearForm = new EventEmitter();

  constructor(
    private fb: FormBuilder,
    public inputValidatorService: InputValidatorService,
    private resourceCenterService: ResourceCenterService,
    private resultHandler: ResultHandlerService) {}

  ngOnInit() {
    this.resourceLink$.subscribe(r => {
      this.initializeForm();
      this.resourceLink = Object.assign({}, r);
    });
  }

  public showUpload(): boolean {
    return this.resourceLink != null && (this.resourceLink.type?.toString() == "PDF" || this.resourceLink.type?.toString() == "Video");
  }

  public showUrl(): boolean {
    return this.resourceLink != null && this.resourceLink.type?.toString() == "Link";
  }

  initializeForm() {
    this.form = this.fb.group({
      regionIds: [this._resourceLinkSubject.getValue()?.regionIds, Validators.required],
      type: [this._resourceLinkSubject.getValue()?.type, Validators.required],
      title: [this._resourceLinkSubject.getValue()?.title, Validators.required],
      url: [this._resourceLinkSubject.getValue()?.url, Validators.pattern(CustomValidators.urlRegex)]
    });
  }

  public setResourceLink(resourceLink: ResourceLinkCreateDTO): void {
    this._resourceLinkSubject.next(resourceLink);
  }

  public saveResourceLink(): void {
    this.form.markAllAsTouched();
    if (!this.form.valid) {
      return;
    }

    let model = this.form.value as ResourceLinkCreateDTO;
    model.toolId = this.toolId;
    model.file = this.upload?.getFile();

    if (this._resourceLinkSubject.getValue()?.id != null && this._resourceLinkSubject.getValue()!.id > 0) {
      this.resourceCenterService.updateResourceLink(this._resourceLinkSubject.getValue()!.id, model).subscribe((data) => {
        this.resultHandler.handle(data).then((success) => {
          if (success) {
            this.clearForm();
            this.onSaveResourceLink.emit(model);
          }
        });
      }, (err) => this.resultHandler.handle(err.error));
    } else {
      this.resourceCenterService.createResourceLink(model).subscribe((data) => {
        this.resultHandler.handle(data).then((success) => {
          if (success) {
            this.clearForm();
            this.onSaveResourceLink.emit(model);
          }
        });
      }, (err) => this.resultHandler.handle(err.error));
    }
  }

  public clearForm(): void {
    this.upload?.reset();
    this.form.reset();
    this.onClearForm.emit();
  }

  public changeFields($event: any): void {
    this.resourceLink!.type = $event.itemData.value;
    this.resourceLink!.url = '';
    this.resourceLink!.filePath = '';
    this.upload?.reset();
  }

  public getAllowedExtensions(): string {
    return this.resourceLink?.type.toString() == "PDF" ? ".pdf" : ".mp4, .mov, .wmv, .avi, .avchd, .flv, .f4v, .swf, .mkv, .webm, .mpeg-2";
  }

}
