import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { BaseGridComponent } from '../../../../shared/components/base-grid/base-grid.component';
import { ServiceResultDTO } from '../../../../core/interfaces/service-result/service-result.dto';
import { ResultHandlerService } from '../../../../core/services/result.handler.service';
import { UserListDTO } from '../../../user-management/interfaces/user-list.dto';
import { UserService } from '../../services/user.service';
import { AccountService } from '../../../../authentication/services/account.service';
import { ApplicationUrls } from 'src/app/shared/constants/application-urls';
import { UrlUtils } from 'src/app/shared/utilities/url-utils';

@Component({
  selector: 'app-user-list',
  templateUrl: './user-list.component.html',
  styleUrls: ['./user-list.component.scss']
})
export class UserListComponent extends BaseGridComponent implements OnInit {
  replaceParams = UrlUtils.replaceParams;
  newUserUrl = ApplicationUrls.GlobalToolSuite.UserManagement.NewAdministrator.path;
  contactInfoUrl = ApplicationUrls.GlobalToolSuite.UserManagement.ContactInfo.path;

  public data!: UserListDTO[];

  constructor(
    private activatedRoute: ActivatedRoute,
    private resultHandler: ResultHandlerService,
    private userService: UserService,
    accountService: AccountService
  ) {
    super(accountService);

    var users = this.activatedRoute.snapshot.data["users"] as ServiceResultDTO<UserListDTO[]>;
    this.handleUsers(users);
  }

  ngOnInit(): void {
    this.filterSettings = { type: 'CheckBox' };
    this.searchSettings = { fields: ['name'] };
  }

  handleUsers(users: ServiceResultDTO<UserListDTO[]>): void {
    if (users.isSuccess) {
      this.data = users.value;
      this.setupPagination(this.data);
    } else {
      this.resultHandler.handle(users);
    }
  }

  loadUsers(): void {
    this.userService.getUsers(!this.showArchived).subscribe((data) => {
      this.handleUsers(data);
    }, (err) => this.resultHandler.handle(err.error));
  }

  onArchiveChange(showArchived: boolean): void {
    this.showArchived = showArchived;
    this.loadUsers();
  }

  onRestoreUser(user: UserListDTO): void {
    this.userService.validateRestore(user).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.restoreUser(user);
        }
      });
    }, (err) => {
      this.resultHandler.handle(err.error).then((success) => {
        if (success) {
          this.restoreUser(user);
        }
      });
    });
  }

  restoreUser(user: UserListDTO): void {
    this.userService.restoreUser(user).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.loadUsers();
        }
      });
    }, (err) => {
      this.resultHandler.handle(err.error);
    });
  }

  onDeleteUser(user: UserListDTO): void {
    this.userService.validateDelete(user).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.deleteUser(user);
        }
      });
    }, (err) => {
      this.resultHandler.handle(err.error).then((success) => {
        if (success) {
          this.deleteUser(user);
        }
      });
    });
  }

  deleteUser(user: UserListDTO): void {
    this.userService.deleteUser(user.id).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.loadUsers();
        }
      });
    }, (err) => {
      this.resultHandler.handle(err.error);
    });
  }
}
