import { UserSecurityUpdateDTO } from './../interfaces/user-update-security.dto';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { ServiceResultDTO } from '../../../core/interfaces/service-result/service-result.dto';
import { UserDTO } from '../interfaces/user.dto';
import { UserCreateDTO } from '../interfaces/user-create.dto';
import { UserListDTO } from '../interfaces/user-list.dto';
import { UserUpdateDTO } from '../interfaces/user-update.dto';
import { UserApiService } from './user.api.service';
import { UserChangePasswordDTO } from '../interfaces/user-change-password.dto';

@Injectable({
  providedIn: 'root'
})
export class UserService {

  constructor(private userApiService: UserApiService) { }

  public getUsers(active: boolean, hasRoles?: boolean, toolId?: number): Observable<ServiceResultDTO<UserListDTO[]>> {
    return this.userApiService.getUsers(active, hasRoles, toolId);
  }

  public getUser(id: number): Observable<ServiceResultDTO<UserDTO>> {
    return this.userApiService.getUser(id);
  }

  public createUser(user: UserCreateDTO): Observable<ServiceResultDTO<UserDTO>> {
    return this.userApiService.createUser(user);
  }

  public updateUser(id: number, user: UserUpdateDTO): Observable<ServiceResultDTO<UserDTO>> {
    return this.userApiService.updateUser(id, user);
  }

  public updateUserSecurity(id: number, user: UserSecurityUpdateDTO): Observable<ServiceResultDTO<UserDTO>> {
    return this.userApiService.updateUserSecurity(id, user);
  }

  public validateDelete(user: UserListDTO): Observable<ServiceResultDTO<boolean>> {
    return this.userApiService.validateDelete(user);
  }

  public deleteUser(id: number): Observable<ServiceResultDTO<boolean>> {
    return this.userApiService.deleteUser(id);
  }

  public validateRestore(user: UserListDTO): Observable<ServiceResultDTO<boolean>> {
    return this.userApiService.validateRestore(user);
  }

  public changePassword(model: UserChangePasswordDTO): Observable<ServiceResultDTO<boolean>> {
    return this.userApiService.changePassword(model);
  }

  public restoreUser(user: UserListDTO): Observable<ServiceResultDTO<boolean>> {
    return this.userApiService.restoreCarrier(user.id);
  }

  public validateUpdateRoles(userId: number, toolId: number, roles: number[]): Observable<ServiceResultDTO<boolean>> {
    return this.userApiService.validateUpdateRoles(userId, toolId, roles);
  }

  public updateRoles(userId: number, toolId: number, roles: number[]): Observable<ServiceResultDTO<UserDTO>> {
    return this.userApiService.updateRoles(userId, toolId, roles);
  }
}
