import { Component, Input, OnInit } from '@angular/core';
import { faScrewdriverWrench } from '@fortawesome/pro-solid-svg-icons';
import { isGlobalToolSuite } from '../../../shared/utilities/shared';

@Component({
  selector: 'tool-icon',
  templateUrl: './tool-icon.component.html',
  styleUrls: ['./tool-icon.component.scss']
})
export class ToolIconComponent implements OnInit {
  isGlobalToolSuite = isGlobalToolSuite;
  faScrewdriverWrench = faScrewdriverWrench;

  @Input()
  public tool: any;

  constructor() { }

  ngOnInit(): void {
  }

  hasLogo(tool: any) {
    return tool.primaryLogoFilePath != null && tool.primaryLogoFilePath != '';
  }
}
