import { Injectable } from '@angular/core';
import { Resolve, RouterStateSnapshot, ActivatedRouteSnapshot } from '@angular/router';
import { Observable } from 'rxjs';
import { ServiceResultDTO } from '../../../core/interfaces/service-result/service-result.dto';
import { ToolDTO } from '../../../shared/interfaces/tools/tool.dto';
import { ToolApiService } from '../services/tool.api.service';

@Injectable()
export class ToolManageableListResolver implements Resolve<ServiceResultDTO<ToolDTO[]>> {

  constructor(private toolService: ToolApiService) {
  }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<ServiceResultDTO<ToolDTO[]>> {
    return this.toolService.getManageableTools(true);
  }
}
