import { ResourceCenterDTO } from './../../../../shared/interfaces/resource-center/resource-center.dto';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { RowDDService, SelectionService } from '@syncfusion/ej2-angular-grids';
import { ServiceResultDTO } from 'src/app/core/interfaces/service-result/service-result.dto';
import { ResultHandlerService } from 'src/app/core/services/result.handler.service';
import { BaseGridComponent } from 'src/app/shared/components/base-grid/base-grid.component';
import { ToolDTO } from 'src/app/shared/interfaces/tools/tool.dto';
import { SharedService } from 'src/app/shared/services/shared.service';
import { ResourceCenterService } from '../../services/resource-center.service';
import { ApplicationUrls } from 'src/app/shared/constants/application-urls';
import { UrlUtils } from 'src/app/shared/utilities/url-utils';

@Component({
  selector: 'app-resource-center',
  templateUrl: './resource-center.component.html',
  styleUrls: ['./resource-center.component.scss'],
  providers: [RowDDService, SelectionService]
})
export class ResourceCenterComponent extends BaseGridComponent implements OnInit {
  replaceParams = UrlUtils.replaceParams;
  editToolUrl = ApplicationUrls.GlobalToolSuite.ResourceCenter.EditTool.path;

  public data!: ToolDTO[];
  public selectOptions: Object | undefined;
  public pageDescription: string = "";

  constructor(
    route: ActivatedRoute,
    private resultHandler: ResultHandlerService,
    private resourceCenterService: ResourceCenterService,
    private sharedService: SharedService
  ) {
    super();

    var data = route.snapshot.data["tools"] as ServiceResultDTO<ToolDTO[]>;
    this.handleTools(data);
  }

  handleTools(tools: ServiceResultDTO<ToolDTO[]>) {
    if (tools.isSuccess) {
      this.data = tools.value;
      this.setupPagination(this.data);
    } else {
      this.resultHandler.handle(tools);
    }
  }

  loadTools(): void {
    this.sharedService.getTools(undefined, undefined, false, !this.showArchived).subscribe((data) => {
      this.handleTools(data);
    }, (err) => this.resultHandler.handle(err.error));
  }

  ngOnInit(): void {
    this.selectOptions = { type: 'Multiple' };

    this.resetDescription();
  }

  onSubmit(): void {
    let model: ResourceCenterDTO = {
      description: this.pageDescription
    };

    this.resourceCenterService.setResourceCenter(model).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.pageDescription = data.value.description;
        }
      });
    }, (err) => {
      this.resultHandler.handle(err.error);
    });
  }

  resetDescription(): void {
    this.sharedService.getResourceCenter().subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.pageDescription = data.value?.description;
        }
      });
    }, (err) => {
      this.resultHandler.handle(err.error);
    });
  }
}
