import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

// Components
import { CarrierCreateComponent } from './carrier-management/components/carrier-create/carrier-create.component';
import { CarrierEditComponent } from './carrier-management/components/carrier-edit/carrier-edit.component';
import { CarrierListComponent } from './carrier-management/components/carrier-list/carrier-list.component';
import { CountryListComponent } from './region-management/components/country-list/country-list.component';
import { FileManagerComponent } from './file-management/components/file-manager/file-manager.component';
import { GlobalToolSuiteListComponent } from './global-tool-suite/components/global-tool-suite-list/global-tool-suite-list.component';
import { LogsListComponent } from './logs/components/logs-list/logs-list.component';
import { ResourceCenterComponent } from './resource-center/components/resource-center-list/resource-center.component';
import { ResourceCenterEditComponent } from './resource-center/components/resource-center-edit/resource-center-edit.component';
import { RoleManagementComponent } from './roles/components/role-management/role-management.component';
import { RoleToolsListComponent } from './roles/components/role-tools-list/role-tools-list.component';
import { RoleToolsUserComponent } from './roles/components/role-tools-user/role-tools-user.component';
import { ToolCreateComponent } from './tools-management/components/tool-create/tool-create.component';
import { ToolEditComponent } from './tools-management/components/tool-edit/tool-edit.component';
import { ToolEditGeneralInfoComponent } from './tools-management/components/tool-edit-general-info/tool-edit-general-info.component';
import { ToolEditPermissionComponent } from './tools-management/components/tool-edit-permission/tool-edit-permission.component';
import { ToolEditStateCarrierComponent } from './tools-management/components/tool-edit-state-carrier/tool-edit-state-carrier.component';
import { ToolListComponent } from './tools-management/components/tool-list/tool-list.component';
import { UserCreateComponent } from './user-management/components/user-create/user-create.component';
import { UserEditComponent } from './user-management/components/user-edit/user-edit.component';
import { UserEditRolesComponent } from './user-management/components/user-edit-roles/user-edit-roles.component';
import { UserEditSecurityComponent } from './user-management/components/user-edit-security/user-edit-security.component';
import { UserListComponent } from './user-management/components/user-list/user-list.component';

// Resolvers
import { CarrierEditResolver } from './carrier-management/resolvers/carrier-edit.resolver';
import { CarrierListResolver } from './carrier-management/resolvers/carrier-list.resolver';
import { CountryListResolver } from './region-management/resolvers/country-list.resolver';
import { LogsListResolver } from './logs/resolvers/logs-list.resolver';
import { RoleToolsListResolver } from './roles/resolvers/role-tools-list.resolver';
import { RoleToolUserResolver } from './roles/resolvers/role-tools-user.resolver';
import { ToolEditResolver } from '../shared/resolvers/tool-edit.resolver';
import { ToolListRolesResolver } from '../shared/resolvers/tool-list-roles.resolver';
import { ToolListResolver } from '../shared/resolvers/tool-list.resolver';
import { ToolManageableListResolver } from './tools-management/resolvers/tool-manageable-list.resolver';
import { UserEditResolver } from './user-management/resolvers/user-edit.resolver';
import { UserListResolver } from './user-management/resolvers/user-list.resolver';
import { RoleGuard } from '../authentication/guards/role.guard';
import { Roles } from '../core/constants/role.constants';

const childRoutes: Routes = [
  { path: '', component: GlobalToolSuiteListComponent  },
  {
    path: 'user-management',
    children: [
      { path: '', component: UserListComponent, resolve: { users: UserListResolver } },
      { path: 'create', component: UserCreateComponent },
      {
        path: ':id',
        children: [
          { path: 'contact-info', component: UserEditComponent, resolve: { user: UserEditResolver } },
          { path: 'roles', component: UserEditRolesComponent, resolve: { user: UserEditResolver } },
          { path: 'security', component: UserEditSecurityComponent, resolve: { user: UserEditResolver } }
        ]
      }
    ]
  },
  {
    path: 'roles',
    children: [
      { path: '', component: RoleManagementComponent, resolve: { tools: ToolListRolesResolver } },
      { path: 'tool/:toolId', component: RoleToolsListComponent, resolve: { roles: RoleToolsListResolver } },
      { path: 'tool/:toolId/user/:userId', component: RoleToolsUserComponent, resolve: { user: RoleToolUserResolver } },
      { path: 'tool/:toolId/assign-user', component: RoleToolsUserComponent }
    ]
  },
  { path: 'logs', component: LogsListComponent, resolve: { logs: LogsListResolver }, canActivate: [RoleGuard], data: { roles: [Roles.SuperAdmin] } },
  { path: 'file-manager', component: FileManagerComponent, canActivate: [RoleGuard], data: { roles: [Roles.SuperAdmin] } },
  { path: 'state-management', component: CountryListComponent, resolve: { countries: CountryListResolver } },
  {
    path: 'carrier-management',
    children: [
      { path: '', component: CarrierListComponent, resolve: { carriers: CarrierListResolver } },
      { path: 'create', component: CarrierCreateComponent },
      { path: ':id', component: CarrierEditComponent, resolve: { carrier: CarrierEditResolver } }
    ]
  },
  {
    path: 'tools-management',
    children: [
      { path: '', component: ToolListComponent, resolve: { tools: ToolManageableListResolver } },
      { path: 'create', component: ToolCreateComponent },
      {
        path: ':id',
        children: [
          { path: '', component: ToolEditComponent, resolve: { tool: ToolEditResolver } },
          { path: 'state-carrier', component: ToolEditStateCarrierComponent, resolve: { tool: ToolEditResolver } },
          { path: 'general-info', component: ToolEditGeneralInfoComponent, resolve: { tool: ToolEditResolver } },
          { path: 'permission', component: ToolEditPermissionComponent, resolve: { tool: ToolEditResolver } }
        ]
      }
    ],
  },
  {
    path: 'resource-center',
    children: [
      { path: '', component: ResourceCenterComponent, resolve: { tools: ToolListResolver } },
      {
        path: ':id',
        children: [ { path: '', component: ResourceCenterEditComponent, resolve: { tool: ToolEditResolver } } ]
      }
    ]
  }
]

@NgModule({
  declarations: [],
  imports: [RouterModule.forChild(childRoutes)],
  exports: [RouterModule]
})

export class AdminRoutingModule { }
