import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ResultHandlerService } from '../../../../core/services/result.handler.service';
import { ServiceResultDTO } from '../../../../core/interfaces/service-result/service-result.dto';
import { ToolDTO } from '../../../../shared/interfaces/tools/tool.dto';
import { formatAdminToolName, isGlobalToolSuite } from '../../../../shared/utilities/shared';
import { ApplicationUrls } from 'src/app/shared/constants/application-urls';
import { UrlUtils } from 'src/app/shared/utilities/url-utils';

@Component({
  selector: 'app-role-management',
  templateUrl: './role-management.component.html',
  styleUrls: ['./role-management.component.scss']
})
export class RoleManagementComponent implements OnInit {
  replaceParams = UrlUtils.replaceParams;
  roleToolManagementUrl = ApplicationUrls.GlobalToolSuite.RoleManagement.RoleToolManagement.path;
  formatAdminToolName = formatAdminToolName;
  isGlobalToolSuite = isGlobalToolSuite;

  public tools!: ToolDTO[];

  constructor(
    private activatedRoute: ActivatedRoute,
    private resultHandler: ResultHandlerService
  ) { }

  ngOnInit(): void {
    var tools = this.activatedRoute.snapshot.data["tools"] as ServiceResultDTO<ToolDTO[]>;
    if (tools.isSuccess) {
      this.tools = tools.value.sort((a, b) => a.id > b.id ? 1 : -1);
    } else {
      this.resultHandler.handle(tools);
    }
  }
}
