import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ServiceResultDTO } from '../../../../core/interfaces/service-result/service-result.dto';
import { ResultHandlerService } from '../../../../core/services/result.handler.service';
import { ToolService } from '../../services/tool.service';
import { BaseGridComponent } from '../../../../shared/components/base-grid/base-grid.component';
import { ToolDTO } from '../../../../shared/interfaces/tools/tool.dto';
import { ApplicationUrls } from 'src/app/shared/constants/application-urls';
import { UrlUtils } from 'src/app/shared/utilities/url-utils';

@Component({
  selector: 'app-tools-list',
  templateUrl: './tool-list.component.html',
  styleUrls: ['./tool-list.component.scss']
})
export class ToolListComponent extends BaseGridComponent implements OnInit {
  replaceParams = UrlUtils.replaceParams;
  newToolUrl = ApplicationUrls.GlobalToolSuite.ToolsManagement.NewExternalTool.path;
  editToolUrl = ApplicationUrls.GlobalToolSuite.ToolsManagement.EditTool.path;
  stateCarrierUrl = ApplicationUrls.GlobalToolSuite.ToolsManagement.StateAndCarrier.path;
  generalInfoUrl = ApplicationUrls.GlobalToolSuite.ToolsManagement.GeneralInformation.path;

  public data!: ToolDTO[];

  constructor(
    route: ActivatedRoute,
    private resultHandler: ResultHandlerService,
    private toolService: ToolService
  ) {
    super();

    var data = route.snapshot.data["tools"] as ServiceResultDTO<ToolDTO[]>;
    this.handleTools(data);
  }

  ngOnInit(): void {
    this.searchSettings = { fields: ['name'] };
  }

  handleTools(tools: ServiceResultDTO<ToolDTO[]>) {
    if (tools.isSuccess) {
      this.data = tools.value;
    } else {
      this.resultHandler.handle(tools);
    }
  }

  loadTools(): void {
    this.toolService.getManageableTools(!this.showArchived).subscribe((data) => {
      this.handleTools(data);
    }, (err) => this.resultHandler.handle(err.error));
  }

  onArchiveChange(showArchived: boolean): void {
    this.showArchived = showArchived;
    this.loadTools();
  }

  onRestore(tool: ToolDTO): void {
    this.toolService.validateRestore(tool).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) this.restoreTool(tool);
      });
    }, (err) => {
      this.resultHandler.handle(err.error).then((success) => {
        if (success) this.restoreTool(tool)
      });
    });
  }

  restoreTool(tool: ToolDTO): void {
    this.toolService.restoreTool(tool).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.loadTools();
        }
      });
    }, (err) => this.resultHandler.handle(err.error));
  }

  onDelete(tool: ToolDTO): void {
    this.toolService.validateDelete(tool).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) this.deleteTool(tool);
      });
    }, (err) => {
      this.resultHandler.handle(err.error).then((success) => {
        if (success) this.deleteTool(tool);
      });
    });
  }

  deleteTool(tool: ToolDTO): void {
    this.toolService.delete(tool).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) this.loadTools();
      });
    }, (err) => this.resultHandler.handle(err.error));
  }

  formatTool(tool: ToolDTO): any {
    const state: ToolDTO = {
      active: tool.active,
      description: tool.description,
      descriptionView: tool.descriptionView,
      isFavoritedByDefault: tool.isFavoritedByDefault,
      isFavoritedByDefaultView: tool.isFavoritedByDefaultView,
      id: tool.id,
      isAdminTool: tool.isAdminTool,
      isExternal: tool.isExternal,
      name: tool.name,
      permissionGroupsIds: [],
      order: tool.order,
      primaryLogoFilePath: tool.primaryLogoFilePath,
      fullSizeLogoFilePath: tool.fullSizeLogoFilePath,
      regionsIds: [],
      resourceLinks: tool.resourceLinks,
      type: tool.type,
      url: tool.url,
      welcomeMessage: tool.welcomeMessage,
      toolsManagement: [],
      isManagedByStateAndCarrier: tool.isManagedByStateAndCarrier
    };

    return { data: state };
  }

  onRowDrop($event: any) {
    const tool = $event.data[0] as ToolDTO;
    const newOrder = $event.dropIndex + 1;

    if (tool.order == newOrder) return;

    this.toolService.updateToolOrder(tool.id, newOrder).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) this.loadTools();
      });
    }, (err) => this.resultHandler.handle(err.error));
  }
}
