import { FormControl, FormGroup, Validators } from "@angular/forms";
import { FormValidators } from '@syncfusion/ej2-angular-inputs';
import { CarrierCreateDTO } from "../interfaces/carrier-create.dto";

export function getCarrierCreateFormGroup(carrier?: CarrierCreateDTO): FormGroup {
  return new FormGroup({
    name: new FormControl(carrier ? carrier.name : '', [FormValidators.required])
  });
}
