import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { CountryApiService } from './country.api.service';
import { CountryDTO } from '../interfaces/country.dto';
import { ServiceResultDTO } from '../../../core/interfaces/service-result/service-result.dto';

@Injectable()
export class CountryService {

  constructor(private countryApiService: CountryApiService) { }

  public get(): Observable<ServiceResultDTO<CountryDTO[]>> {
    return this.countryApiService.get();
  }
}
