import { ActivatedRoute } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { environment } from './../../../../../environments/environment';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { InputValidatorService } from 'src/app/shared/services/input-validator.service';
import { Location } from '@angular/common';
import { ResultHandlerService } from '../../../../core/services/result.handler.service';
import { ServiceResultDTO } from '../../../../core/interfaces/service-result/service-result.dto';
import { UserDTO } from '../../interfaces/user.dto';
import { UserSecurityUpdateDTO } from '../../interfaces/user-update-security.dto';
import { UserService } from '../../services/user.service';
import { UserChangePasswordDTO } from '../../interfaces/user-change-password.dto';
import { NavService } from 'src/app/core/services/nav.service';
import { ForgotPasswordDTO } from '../../../../authentication/interfaces/account/forgot-password.dto';
import { AccountService } from '../../../../authentication/services/account.service';

@Component({
  selector: 'app-user-edit-security',
  templateUrl: './user-edit-security.component.html',
  styleUrls: ['./user-edit-security.component.scss']
})
export class UserEditSecurityComponent implements OnInit {
  form!: FormGroup;
  public isEditable: boolean = false;
  public user!: UserDTO;

  constructor(
    route: ActivatedRoute,
    private fb: FormBuilder,
    public inputValidatorService: InputValidatorService,
    private location: Location,
    private resultHandler: ResultHandlerService,
    private userService: UserService,
    private accountService: AccountService,
    navService: NavService
  )
  {
    const data = route.snapshot.data["user"] as ServiceResultDTO<UserDTO>;
    if (data.isSuccess) {
      this.user = data.value;
      this.isEditable = data.value.userType == 1; // "Administrator";
      navService.setUserManagementNavMenu(this.user.userTypeName);
    } else {
      this.resultHandler.handle(data);
    }
  }

  ngOnInit() {
    this.initializeForm();
  }

  return = () => this.location.back();

  public onSaveUser(): void {
    this.form.markAllAsTouched();
    if (!this.form.valid) {
      return;
    }

    let model = this.form.value as UserSecurityUpdateDTO;
    this.userService.updateUserSecurity(this.user.id, model).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.return();
        }
      });
    }, (err) => {
      this.resultHandler.handle(err.error);
    });
  }

  public resetPassword(): void {
    let model: ForgotPasswordDTO = {
      clientUri: environment.client.baseUrl,
      email: this.user.email
    };

    this.accountService.forgotPassword(model).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.return();
        }
      });
    }, (err) => {
      this.resultHandler.handle(err.error);
    })
  }

  public changePassword(model: UserChangePasswordDTO): void {
    model.email = this.user?.email;

     this.userService.changePassword(model).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.return();
        }
      });
    }, (err) => {
      this.resultHandler.handle(err.error);
    });
  }

  initializeForm() {
    this.form = this.fb.group({
      accountLocked: [this.user.accountLocked, Validators.required],
      phoneNumber: [this.user.phoneNumber, Validators.required]
    });
  }
}
