import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CountryDTO } from '../../interfaces/country.dto';
import { RegionUpdateDTO } from '../../interfaces/region.update.dto';
import { ServiceResultDTO } from '../../../../core/interfaces/service-result/service-result.dto';
import { CountryService } from '../../services/country.service';
import { RegionService } from '../../services/region.service';
import { ResultHandlerService } from '../../../../core/services/result.handler.service';

@Component({
  selector: 'app-country-list',
  templateUrl: './country-list.component.html',
  styleUrls: ['./country-list.component.scss']
})
export class CountryListComponent implements OnInit {
  public countries!: CountryDTO[];

  constructor(
    route: ActivatedRoute,
    private countryService: CountryService,
    private regionService: RegionService,
    private resultHandler: ResultHandlerService
  ) {
    var data = route.snapshot.data["countries"] as ServiceResultDTO<CountryDTO[]>;
    if (data.isSuccess) {
      this.countries = data.value;
    } else {
      this.resultHandler.handle(data);
    }
  }

  ngOnInit(): void {
  }

  resetRegions(): void {
    this.countryService.get().subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.countries = data.value;
        }
      });
    }, (err) => {
      this.resultHandler.handle(err.error);
    });
  }

  onSubmit(): void {
    var regions = this.countries.flatMap(a => a.regions).map((r) => {
      return { id: r.id, active: r.active };
    });
    this.regionService.validateUpdate(regions).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.updateRegions(regions);
        }
      });
    }, (err) => {
      this.resultHandler.handle(err.error).then((success) => {
        if (success) {
          this.updateRegions(regions);
        }
      });
    });
  }

  updateRegions(regions: RegionUpdateDTO[]): void {
    this.regionService.update(regions).subscribe((data) => {
      this.resultHandler.handle(data);
    }, (err) => {
      this.resultHandler.handle(err.error);
    });
  }
}
