import { Injectable } from '@angular/core';
import { Resolve, RouterStateSnapshot, ActivatedRouteSnapshot } from '@angular/router';
import { Observable } from 'rxjs';
import { ServiceResultDTO } from '../../../core/interfaces/service-result/service-result.dto';
import { UserListDTO } from '../../user-management/interfaces/user-list.dto';
import { UserApiService } from '../services/user.api.service';

@Injectable()
export class UserListResolver implements Resolve<ServiceResultDTO<UserListDTO[]>> {

  constructor(private userService: UserApiService) {
  }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<ServiceResultDTO<UserListDTO[]>> {
    return this.userService.getUsers(true);
  }
}
