import { Injectable } from '@angular/core';
import { Resolve, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs';
import { CarrierService } from '../services/carrier.service';
import { CarrierDTO } from '../interfaces/carrier.dto';
import { ServiceResultDTO } from '../../../core/interfaces/service-result/service-result.dto';

@Injectable()
export class CarrierListResolver implements Resolve<ServiceResultDTO<CarrierDTO[]>> {

  constructor(private carrierService: CarrierService) {
  }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<ServiceResultDTO<CarrierDTO[]>> {
    return this.carrierService.getCarriers(true);
  }
}
