import { Injectable } from "@angular/core";
import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from "@angular/router";
import { Observable } from "rxjs";
import { ServiceResultDTO } from "../../../core/interfaces/service-result/service-result.dto";
import { UserRoleDTO } from "../interfaces/user-role.dto";
import { RoleService } from "../services/role.service";

@Injectable()
export class RoleToolsListResolver implements Resolve<ServiceResultDTO<UserRoleDTO[]>> {

  constructor(private roleService: RoleService) {
  }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<ServiceResultDTO<UserRoleDTO[]>> {
    const id = Number(route.paramMap.get('toolId'));
    return this.roleService.getUsersRoles(id);
  }
}
