import { Injectable } from '@angular/core';
import { Resolve, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs';
import { ServiceResultDTO } from '../../../core/interfaces/service-result/service-result.dto';
import { LogDetailDTO } from '../interfaces/log-detail.dto';
import { LogsService } from '../services/logs.service';

@Injectable()
export class LogsListResolver implements Resolve<ServiceResultDTO<LogDetailDTO[]>> {

  constructor(private logsService: LogsService) {
  }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<ServiceResultDTO<LogDetailDTO[]>> {
    return this.logsService.get();
  }
}
