import { Component, EventEmitter, Input, OnInit, Output, QueryList, ViewChildren } from '@angular/core';
import { ResultHandlerService } from '../../../core/services/result.handler.service';
import { UserService } from '../../user-management/services/user.service';
import { RoleGroupedDTO } from '../../roles/interfaces/role-grouped.dto';
import { RoleListDTO } from '../../roles/interfaces/role-list.dto';
import { RoleToolDTO } from '../../roles/interfaces/role-tool.dto';
import { UserDTO } from '../../user-management/interfaces/user.dto';
import { CheckBoxComponent } from '@syncfusion/ej2-angular-buttons';
import { RestrictedComponent } from 'src/app/shared/components/restricted/restricted.component';
import { AccountService } from 'src/app/authentication/services/account.service';

@Component({
  selector: 'app-user-edit-roles-tool',
  templateUrl: './user-edit-roles-tool.component.html'
})
export class UserEditRolesToolComponent extends RestrictedComponent implements OnInit {
  @Input()
  public user?: UserDTO;
  @Input()
  public roleGroup!: RoleGroupedDTO;
  @Input()
  public undoButtonText: string = 'Undo';
  @Input()
  public saveButtonText: string = 'Save';

  @Output()
  onUndoClicked = new EventEmitter();
  @Output()
  onUserSaved = new EventEmitter<UserDTO>();

  @ViewChildren("roleOption")
  private roleOptions!: QueryList<CheckBoxComponent>;

  public checkedRoles: any = null;

  constructor(
    protected resultHandler: ResultHandlerService,
    protected userService: UserService,
    protected accountService: AccountService
  ) {
    super(accountService);
  }

  ngOnInit(): void { }

  hasRole(role: RoleListDTO) {
    return this.user != null && this.user.roles != null && this.user.roles.includes(role.id);
  }

  undoRoleChanges(tool: RoleToolDTO) {
    tool.regions.forEach(tr => {
      tr.roles.forEach(trr => {
        const element = this.getRoleElement(trr.id);
        if (element != null) element.checked = this.user != null && this.user.roles && this.user.roles.includes(trr.id);
      });
    });
    tool.groups.forEach(tg => {
      tg.roles.forEach(tgr => {
        const element = this.getRoleElement(tgr.id);
        if (element != null) element.checked = this.user != null && this.user.roles && this.user.roles.includes(tgr.id);
      });
    });

    this.onUndoClicked.emit();
  }

  isRoleChecked(id: number): boolean {
    const element = this.getRoleElement(id);
    return element != null && element.checked;
  }

  getRoleElement(roleId: number): CheckBoxComponent | null {
    const element = this.roleOptions.filter(r => r.element.className.includes(`role${roleId}`));
    return element.length > 0 ? element[0] : null;
  }

  saveRoleChanges(tool: RoleToolDTO) {
    var roles: number[] = [];

    tool.regions.forEach(tr => {
      tr.roles.forEach(trr => {
        if (this.isRoleChecked(trr.id)) roles.push(trr.id);
      });
    });
    tool.groups.forEach(tg => {
      tg.roles.forEach(tgr => {
        if (this.isRoleChecked(tgr.id)) roles.push(tgr.id);
      });
    });

    this.userService.validateUpdateRoles(this.user != null ? this.user.id : 0, tool.id, roles).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) this.updateRoles(tool, roles);
      });
    }, (err) => {
      this.resultHandler.handle(err.error).then((success) => {
        if (success) this.updateRoles(tool, roles);
      });
    });
  }

  updateRoles(tool: RoleToolDTO, roles: number[]) {
    if (this.user == null) return;

    this.userService.updateRoles(this.user.id, tool.id, roles).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.user = data.value;
          this.onUserSaved.emit(data.value);
        }
      });
    }, (err) => this.resultHandler.handle(err.error));
  }
}
