import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { ResultHandlerService } from '../../../../core/services/result.handler.service';
import { ToolService } from '../../services/tool.service';
import { ServiceResultDTO } from '../../../../core/interfaces/service-result/service-result.dto';
import { ToolDTO } from '../../../../shared/interfaces/tools/tool.dto';
import { ToolUpdateDTO } from '../../interfaces/tool-update.dto';
import { NavService } from 'src/app/core/services/nav.service';

@Component({
  selector: 'app-tool-edit',
  template: '<tool-external [tool]="tool" (onToolSaved)="onSaveTool($event)"></tool-external>'
})
export class ToolEditComponent implements OnInit {
  public isExternal: boolean = false;
  public tool!: ToolDTO;

  constructor(
    route: ActivatedRoute,
    private location: Location,
    private resultHandler: ResultHandlerService,
    private toolService: ToolService,
    navService: NavService
  ) {
    const data = route.snapshot.data["tool"] as ServiceResultDTO<ToolDTO>;
    if (data.isSuccess) {
      this.tool = data.value;
      this.isExternal = this.tool.isExternal;
      navService.setToolNavMenu(this.tool);
    } else {
      this.resultHandler.handle(data);
    }
  }

  return = () => this.location.back();

  ngOnInit(): void {
  }

  public onSaveTool(eventData: any): void {
    const formValues = eventData.formData;
    const tool: ToolUpdateDTO = {
      name: formValues.name.trim(),
      description: formValues.description.trim(),
      url: formValues.url.trim(),
      isFavoritedByDefault: formValues.isFavoritedByDefault,
      permissionGroupsIds: eventData.permissionGroupsIds,
      regionsIds: eventData.regionsIds
    }

    this.toolService.validateUpdate(this.tool.id, tool, eventData.primaryLogo).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) this.update(tool, eventData.primaryLogo, eventData.fullSizeLogo);
      });
    }, (err) => {
      this.resultHandler.handle(err.error).then((success) => {
        if (success) this.update(tool, eventData.primaryLogo, eventData.fullSizeLogo);
      });
    });
  }

  public update(tool: ToolUpdateDTO, primaryLogo?: File, fullSizeLogo?: File): void {
    this.toolService.updateTool(this.tool.id, tool, primaryLogo, fullSizeLogo).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) this.return();
      });
    }, (err) => this.resultHandler.handle(err.error));
  }
}
