import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { InputValidatorService } from 'src/app/shared/services/input-validator.service';
import { UserChangePasswordDTO } from 'src/app/admin/user-management/interfaces/user-change-password.dto';


@Component({
  selector: 'app-user-edit-password',
  templateUrl: './user-edit-password.component.html'
})
export class UserEditPasswordComponent implements OnInit {
  form!: FormGroup;

  @Input()
  public email: string = "";

  @Input()
  public token: string = "";

  @Output()
  onEditPassword = new EventEmitter<UserChangePasswordDTO>();

  constructor(private fb: FormBuilder,
    public inputValidatorService: InputValidatorService) { }

  ngOnInit() {
    this.initializeForm();
  }

  public editPassword(): void {
    this.form.markAllAsTouched();
    if (!this.form.valid) {
      return;
    }

    let model = this.form.value as UserChangePasswordDTO;
    this.onEditPassword.emit(model);
  }

  initializeForm() {
    this.form = this.fb.group({
      confirmPassword: ["", Validators.required],
      password: ["", Validators.required]
    });
  }
}
