import { ResourceLinkComponent } from './../resource-link/resource-link.component';
import { RegionDTO } from './../../../region-management/interfaces/region.dto';
import { ResourceLinkDTO } from './../../interfaces/resource-link.dto';
import { ActivatedRoute } from '@angular/router';
import { Component, OnInit, ViewChild } from '@angular/core';
import { ResultHandlerService } from 'src/app/core/services/result.handler.service';
import { ServiceResultDTO } from 'src/app/core/interfaces/service-result/service-result.dto';
import { ToolDTO } from 'src/app/shared/interfaces/tools/tool.dto';
import { ResourceCenterService } from '../../services/resource-center.service';
import { BaseGridComponent } from '../../../../shared/components/base-grid/base-grid.component';
import { RegionService } from 'src/app/admin/region-management/services/region.service';
import { faPlus } from '@fortawesome/pro-light-svg-icons';
import { DialogComponent } from '@syncfusion/ej2-angular-popups';
import { FormBuilder } from '@angular/forms';
import { InputValidatorService } from 'src/app/shared/services/input-validator.service';
import { ApplicationUrls } from 'src/app/shared/constants/application-urls';
import { UrlUtils } from 'src/app/shared/utilities/url-utils';
import { ResourceLinkCreateDTO } from '../../interfaces/resource-link-create.dto';

@Component({
  selector: 'app-resource-center-edit',
  templateUrl: './resource-center-edit.component.html',
  styleUrls: ['./resource-center-edit.component.scss']
})
export class ResourceCenterEditComponent extends BaseGridComponent implements OnInit {
  replaceParams = UrlUtils.replaceParams;
  internalToolManagementUrl = ApplicationUrls.GlobalToolSuite.ToolsManagement.EditTool.path;
  externalToolManagementUrl = ApplicationUrls.GlobalToolSuite.ToolsManagement.GeneralInformation.path;

  public regions!: RegionDTO[];
  public tool!: ToolDTO;
  public faPlus = faPlus;
  public title!: string;

  @ViewChild('ejDialog')
  public ejDialog!: DialogComponent;

  @ViewChild('resourceLinkComponent')
  public resourceLinkComponent!: ResourceLinkComponent;

  constructor(
    route: ActivatedRoute,
    public inputValidatorService: InputValidatorService,
    private fb: FormBuilder,
    private resourceCenterService: ResourceCenterService,
    private regionService: RegionService,
    private resultHandler: ResultHandlerService
  ) {
    super();

    const data = route.snapshot.data["tool"] as ServiceResultDTO<ToolDTO>;
    this.handleTool(data);
  }

  public fields: Object = { text: 'name', value: 'id' };

  handleTool(tool: ServiceResultDTO<ToolDTO>) {
    if (tool.isSuccess) {
      this.tool = tool.value;
      this.loadResourceLinks();
    } else {
      this.resultHandler.handle(tool);
    }
  }

  handleResourceLinks(resourceLinks: ServiceResultDTO<ResourceLinkDTO[]>) {
    if (resourceLinks.isSuccess) {
      this.tool.resourceLinks = resourceLinks.value;
    } else {
      this.resultHandler.handle(resourceLinks);
    }
  }

  ngOnInit(): void {
    this.regionService.getActiveRegions().subscribe((data) => {
      if (data.isSuccess) {
        this.regions = data.value;
      } else {
        this.resultHandler.handle(data);
      }
    });
  }

  loadResourceLinks(): void {
    this.resourceCenterService.getResourceLinks(this.tool.id).subscribe((data) => {
      this.handleResourceLinks(data);
    }, (err) => this.resultHandler.handle(err.error));
  }

  filterByRegion($event: any): void {
    if ($event.itemData == null) {
      this.loadResourceLinks();
    } else {
      var region = $event.itemData as RegionDTO;

      this.resourceCenterService.getResourceLinks(this.tool.id, region.id).subscribe((data) => {
        this.handleResourceLinks(data);
      }, (err) => this.resultHandler.handle(err.error));
    }
  }

  onRowDrop($event: any) {
    const link = $event.data[0] as ResourceLinkDTO;
    const newOrder = $event.dropIndex + 1;
    if (link.order == newOrder) return;

    this.resourceCenterService.updateResourceLinkOrder($event.data[0].id, newOrder).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.loadResourceLinks();
        }
      });
    }, (err) => this.resultHandler.handle(err.error));
  }

  public onSaveResourceLink(): void {
    this.onOverlayClick();
    this.loadResourceLinks();
  }

  public onOpenDialog(title: string, event?: ResourceLinkDTO): void {
    this.resourceLinkComponent.setResourceLink(event as ResourceLinkCreateDTO);
    this.title = title;
    this.ejDialog.show();
  };

  public onDeleteResourceLink(model: ResourceLinkDTO): void {
    this.resourceCenterService.validateDeleteResourceLink(model).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.deleteResourceLink(model.id);
        }
      });
    }, (err) => {
      this.resultHandler.handle(err.error).then((success) => {
        if (success) {
          this.deleteResourceLink(model.id);
        }
      });
    });
  }

  deleteResourceLink(id: number): void {
    this.resourceCenterService.deleteResourceLink(id).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.loadResourceLinks();
        }
      });
    }, (err) => {
      this.resultHandler.handle(err.error);
    });
  }

  public onOverlayClick(): void {
    this.ejDialog.hide();
  }
}
