import { Component, OnInit, QueryList, ViewChildren } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ResultHandlerService } from '../../../../core/services/result.handler.service';
import { RoleService } from '../../../roles/services/role.service';
import { RoleGroupedDTO } from '../../../roles/interfaces/role-grouped.dto';
import { ServiceResultDTO } from '../../../../core/interfaces/service-result/service-result.dto';
import { UserDTO } from '../../interfaces/user.dto';
import { AccordionComponent } from '@syncfusion/ej2-angular-navigations';
import { formatAdminToolName } from '../../../../shared/utilities/shared';
import { NavService } from 'src/app/core/services/nav.service';

@Component({
  selector: 'app-user-edit-roles',
  templateUrl: './user-edit-roles.component.html',
  styleUrls: ['./user-edit-roles.component.scss']
})
export class UserEditRolesComponent implements OnInit {
  formatToolName = formatAdminToolName;

  public user!: UserDTO;
  public rolesGrouped!: RoleGroupedDTO[];
  public checkedRoles: any = null;

  public isExpanded: boolean = false;
  private expandedCount: number = 0;
  @ViewChildren("accordionComponent") accordionComponents!: QueryList<AccordionComponent>;

  constructor(
    route: ActivatedRoute,
    private resultHandler: ResultHandlerService,
    private roleService: RoleService,
    navService: NavService
  ) {
    const data = route.snapshot.data["user"] as ServiceResultDTO<UserDTO>;
    if (data.isSuccess) {
      this.user = data.value;
      navService.setUserManagementNavMenu(this.user.userTypeName);
    } else {
      this.resultHandler.handle(data);
    }
  }

  ngOnInit(): void {
    this.roleService.getRolesGrouped().subscribe((data) => {
      this.handleRoles(data);
    });
  }

  handleRoles(rolesGrouped: ServiceResultDTO<RoleGroupedDTO[]>): void {
    if (rolesGrouped.isSuccess) {
      this.rolesGrouped = rolesGrouped.value;
    } else {
      this.resultHandler.handle(rolesGrouped);
    }
  }

  toggleAccordions() {
    this.accordionComponents.forEach(a => {
      a.expandItem(!this.isExpanded);
    });
  }

  accordionExpanded(event: any) {
    this.expandedCount = event.isExpanded ? this.expandedCount + 1 : this.expandedCount - 1;

    if (this.expandedCount == 0) {
      this.isExpanded = false;
    } else if (this.expandedCount == this.accordionComponents.length) {
      this.isExpanded = true;
    }
  }
}
