import { Component, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core';
import { Location } from '@angular/common';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { SingleUploadComponent } from '../../../shared/components/single-upload/single-upload.component';
import { ToolDTO } from '../../../shared/interfaces/tools/tool.dto';
import { InputValidatorService } from '../../../shared/services/input-validator.service';
import { RegionListComponent } from '../region-list/region-list.component';
import { UserTypeListComponent } from '../user-type-list/user-type-list.component';
import { CustomValidators } from '../../../shared/utilities/custom-validators';
import { ApplicationUrls } from 'src/app/shared/constants/application-urls';
import { MaxFileSizes } from 'src/app/shared/constants/file-sizes';

@Component({
  selector: 'tool-external',
  templateUrl: './tool-external.component.html',
  styleUrls: ['./tool-external.component.scss']
})
export class ToolExternalComponent implements OnInit {
  returnUrl = ApplicationUrls.GlobalToolSuite.ToolsManagement.path;
  MaxFileSizes = MaxFileSizes;

  public form!: FormGroup;

  @Input()
  public tool!: ToolDTO;

  @Output()
  onToolSaved = new EventEmitter<any>();

  @ViewChild("upload")
  public upload!: SingleUploadComponent;
  @ViewChild("uploadFullSizeLogo")
  public uploadFullSizeLogo!: SingleUploadComponent;
  @ViewChild("regions")
  public regionsComponent!: RegionListComponent;
  @ViewChild("userTypes")
  public userTypesComponent!: UserTypeListComponent;

  constructor(
    private fb: FormBuilder,
    private location: Location,
    public inputValidatorService: InputValidatorService
  ) {
  }

  ngOnInit(): void {
    this.initializeForm();
  }

  return = () => this.location.back();

  public onClear(): void {
    if (this.tool == null) {
      this.form.reset();
    } else {
      this.initializeForm();
    }

    this.userTypesComponent.reset();
    this.regionsComponent.reset();
    this.upload.reset();
    this.uploadFullSizeLogo.reset();
  }

  initializeForm() {
    this.form = this.fb.group({
      name: [this.tool != null ? this.tool.name : '', Validators.required],
      description: [this.tool != null ? this.tool.description : ''],
      url: [this.tool != null ? this.tool.url : null, [Validators.required, Validators.pattern(CustomValidators.urlRegex)]],
      isFavoritedByDefault: [this.tool != null ? this.tool.isFavoritedByDefault : false]
    });
  }

  public onSaveTool(): void {
    this.form.markAllAsTouched();
    if (!this.form.valid) {
      return;
    }

    var saveData = {
      formData: this.form.getRawValue(),
      primaryLogo: this.upload.getFile(),
      fullSizeLogo: this.uploadFullSizeLogo.getFile(),
      permissionGroupsIds: this.userTypesComponent.getSelectedIds(),
      regionsIds: this.regionsComponent.getSelectedIds()
    };

    this.onToolSaved.emit(saveData);
  }
}
