import { Component, Input, OnInit } from '@angular/core';
import { ResultHandlerService } from '../../../core/services/result.handler.service';
import { UserTypeService } from '../../user-management/services/user-type.service';
import { OptionListComponent } from '../../../shared/components/option-list/option-list.component';

@Component({
  selector: 'user-type-list',
  templateUrl: '../../../shared/components/option-list/option-list.component.html',
  styleUrls: ['../../../shared/components/option-list/option-list.component.scss']
})
export class UserTypeListComponent extends OptionListComponent implements OnInit {
  @Input()
  public override title: string = 'Permission Groups';

  constructor(
    private userTypeService: UserTypeService,
    private resultHandler: ResultHandlerService
  ) {
    super();
  }

  ngOnInit(): void {
    this.userTypeService.getUserTypes().subscribe((data) => {
      if (data.isSuccess) {
        this.loadOptions(data.value);
      } else {
        this.resultHandler.handle(data);
      }
    }, (err) => {
      this.resultHandler.handle(err.error);
    });
  }
}
