import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { CarrierApiService } from './carrier.api.service';
import { CarrierDTO } from '../interfaces/carrier.dto';
import { CarrierCreateDTO } from '../interfaces/carrier-create.dto';
import { CarrierUpdateDTO } from '../interfaces/carrier-update.dto';
import { ServiceResultDTO } from '../../../core/interfaces/service-result/service-result.dto';

@Injectable()
export class CarrierService {

  constructor(private carrierApiService: CarrierApiService) { }

  public createCarrier(carrier: CarrierCreateDTO, file?: File) {
    return this.carrierApiService.createCarrier(carrier, file);
  }

  public getCarriers(active: boolean): Observable<ServiceResultDTO<CarrierDTO[]>> {
    return this.carrierApiService.getCarriers(active);
  }

  public getCarrierById(id: number): Observable<ServiceResultDTO<CarrierDTO>> {
    return this.carrierApiService.getCarrier(id);
  }

  public validateUpdate(id: number, carrier: CarrierUpdateDTO, file?: File): Observable<ServiceResultDTO<boolean>> {
    return this.carrierApiService.validateUpdate(id, carrier, file);
  }

  public updateCarrier(id: number, carrier: CarrierUpdateDTO, file?: File): Observable<ServiceResultDTO<CarrierDTO>> {
    return this.carrierApiService.updateCarrier(id, carrier, file);
  }

  public validateDelete(carrier: CarrierDTO): Observable<ServiceResultDTO<boolean>> {
    return this.carrierApiService.validateDelete(carrier);
  }

  public delete(carrier: CarrierDTO): Observable<ServiceResultDTO<boolean>> {
    return this.carrierApiService.delete(carrier.id);
  }

  public validateRestore(carrier: CarrierDTO): Observable<ServiceResultDTO<boolean>> {
    return this.carrierApiService.validateRestore(carrier);
  }

  public restoreCarrier(carrier: CarrierDTO): Observable<ServiceResultDTO<boolean>> {
    return this.carrierApiService.restoreCarrier(carrier.id);
  }
}
