import { Component, OnInit, ViewChild } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { Location } from '@angular/common';
import { CarrierCreateDTO } from '../../interfaces/carrier-create.dto';
import { CarrierService } from '../../services/carrier.service';
import { ResultHandlerService } from '../../../../core/services/result.handler.service';
import { getCarrierCreateFormGroup } from '../../forms/carrier-create-form-group';
import { RegionListComponent } from '../../../shared/region-list/region-list.component';
import { SingleUploadComponent } from '../../../../shared/components/single-upload/single-upload.component';
import { InputValidatorService } from '../../../../shared/services/input-validator.service';
import { MaxFileSizes } from 'src/app/shared/constants/file-sizes';

@Component({
  selector: 'app-carrier-create',
  templateUrl: './carrier-create.component.html',
  styleUrls: ['./carrier-create.component.scss']
})
export class CarrierCreateComponent implements OnInit {
  public form!: FormGroup;
  MaxFileSizes = MaxFileSizes;

  @ViewChild("upload")
  public upload!: SingleUploadComponent;

  @ViewChild("regions")
  public regionsComponent!: RegionListComponent;

  constructor(
    private carrierService: CarrierService,
    private resultHandler: ResultHandlerService,
    private location: Location,
    public inputValidatorService: InputValidatorService
  ) {
  }

  ngOnInit(): void {
    this.initializeForm();
  }

  initializeForm() {
    this.form = getCarrierCreateFormGroup();
  }

  return(): void {
    this.location.back();
  }

  onClear() {
    this.initializeForm();
    this.upload.reset();
    this.regionsComponent.clear();
  }

  onSubmit(): void {
    this.form.markAllAsTouched();
    if (!this.form.valid) {
      return;
    }

    const file = this.upload.getFile();
    const formValues = this.form.getRawValue();
    const carrier: CarrierCreateDTO = {
      name: formValues.name.trim(),
      regionsIds: this.regionsComponent.getSelectedIds()
    }

    this.carrierService.createCarrier(carrier, file).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.return();
        }
      });
    }, (err) => {
      this.resultHandler.handle(err.error);
    });
  }

  get name() { return this.form.get('name'); }
}
