import { Component, Input, OnInit } from '@angular/core';
import { ItemDTO } from '../../../../shared/interfaces/item.dto';
import { RegionDTO } from '../../../region-management/interfaces/region.dto';
import { ToolManagementUpdateDTO } from '../../../../shared/interfaces/tools/tool-management-update.dto';
import { ToolManagementDTO } from '../../../../shared/interfaces/tools/tool-management.dto';
import { FormBuilder, FormGroup } from '@angular/forms';
import { InputValidatorService } from '../../../../shared/services/input-validator.service';
import { CustomValidators } from '../../../../shared/utilities/custom-validators';
import { RestrictedComponent } from '../../../../shared/components/restricted/restricted.component';

@Component({
  selector: 'tool-edit-state-carrier-partial',
  templateUrl: './tool-edit-state-carrier-partial.component.html',
  styleUrls: ['./tool-edit-state-carrier-partial.component.scss']
})
export class ToolEditStateCarrierPartialComponent extends RestrictedComponent implements OnInit {
  form!: FormGroup;

  public carriers?: ItemDTO[] = undefined;

  @Input()
  public region!: RegionDTO;
  @Input()
  public toolManagement: ToolManagementDTO | undefined = undefined;

  currentData!: {
    enabled: boolean,
    selectedCarrierIds: number[],
    changeReportEmails: string,
    analyticsReportEmails: string,
    reportEmails: string,
    isContentHidden: boolean
  };

  constructor(
    private fb: FormBuilder,
    public inputValidatorService: InputValidatorService
  ) {
    super();
  }

  ngOnInit(): void {
    this.setCurrentData();
    this.initializeForm();
    this.loadOptions(this.region.carriers);
  }

  initializeForm() {
    this.form = this.fb.group({
      changeReportEmails: [this.currentData.changeReportEmails, [CustomValidators.multipleEmailsValidator]],
      analyticsReportEmails: [this.currentData.analyticsReportEmails, [CustomValidators.multipleEmailsValidator]],
      reportEmails: [this.currentData.reportEmails, [CustomValidators.multipleEmailsValidator]],
      isContentHidden: [this.currentData.isContentHidden]
    });
  }

  setCurrentData() {
    if (this.toolManagement == undefined) {
      this.currentData = {
        enabled: false,
        selectedCarrierIds: [],
        changeReportEmails: '',
        analyticsReportEmails: '',
        reportEmails: '',
        isContentHidden: false
      };
    } else {
      this.currentData = {
        enabled: true,
        selectedCarrierIds: this.toolManagement.carriers.map(c => c.id),
        changeReportEmails: this.toolManagement.changeReportEmails,
        analyticsReportEmails: this.toolManagement.analyticsReportEmails,
        reportEmails: this.toolManagement.reportEmails,
        isContentHidden: this.toolManagement.isContentHidden
      };
    }
  }

  loadOptions(options: any[]): void {
    this.carriers = options.map(o => {
      return {
        id: o.id,
        name: o.name,
        selected: this.currentData.selectedCarrierIds?.findIndex(sr => sr == o.id) != -1
      }
    });
  }

  getSelectedCarrierIds = (): number[] => this.carriers?.filter(o => o.selected).map(o => o.id) ?? [];

  clear() {
    this.currentData = {
      enabled: false,
      selectedCarrierIds: [],
      changeReportEmails: '',
      analyticsReportEmails: '',
      reportEmails: '',
      isContentHidden: false
    };
    this.initializeForm();
    this.carriers?.forEach(o => { o.selected = false; });
  }

  reset() {
    this.setCurrentData();
    this.initializeForm();
    this.loadOptions(this.region.carriers);
  }

  onEnableChanged(event: any) {
    if (!event.checked) this.clear();
  }

  getModel(): ToolManagementUpdateDTO | null {
    if (!this.currentData.enabled) return null;

    var formData = this.form.getRawValue();

    return {
      analyticsReportEmails: formData.analyticsReportEmails,
      carriers: this.getSelectedCarrierIds().map(id => id),
      changeReportEmails: formData.changeReportEmails,
      region: this.region.id,
      reportEmails: formData.reportEmails,
      isContentHidden: formData.isContentHidden
    };
  }
}
