import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { ServiceResultDTO } from '../../../core/interfaces/service-result/service-result.dto';
import { UserTypeDTO } from '../interfaces/user-type.dto';
import { UserTypeApiService } from './user-type.api.service';

@Injectable({
  providedIn: 'root'
})
export class UserTypeService {

  constructor(private userTypeApiService: UserTypeApiService) { }

  public getUserTypes(): Observable<ServiceResultDTO<UserTypeDTO[]>> {
    return this.userTypeApiService.getUserTypes();
  }
}
