import { HttpClient } from '@angular/common/http';
import { Injectable, Inject } from '@angular/core';
import { BaseAPIService } from '../../../core/api/base.api.service';
import { CountryDTO } from '../interfaces/country.dto';
import { ServiceResultDTO } from '../../../core/interfaces/service-result/service-result.dto';
import { AdminApiEndpoints } from '../../constants/admin-api.constants';

@Injectable({
  providedIn: "root"
})
export class CountryApiService extends BaseAPIService {
  constructor(
    @Inject("BASE_API_URL_WEB") baseApiUrl_Web: string,
    http: HttpClient
  ) {
    super(baseApiUrl_Web, http);
  }

  public get() {
    return this.http.get<ServiceResultDTO<CountryDTO[]>>(
      this.constructRequestUrl(AdminApiEndpoints.GetCountries())
    );
  }
}
