import { Injectable } from '@angular/core';
import { Resolve, RouterStateSnapshot, ActivatedRouteSnapshot } from '@angular/router';
import { empty, Observable } from 'rxjs';
import { ServiceResultDTO } from '../../../core/interfaces/service-result/service-result.dto';
import { UserDTO } from '../../user-management/interfaces/user.dto';
import { UserApiService } from '../../user-management/services/user.api.service';

@Injectable()
export class RoleToolUserResolver implements Resolve<ServiceResultDTO<UserDTO>> {

  constructor(private userService: UserApiService) {
  }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<ServiceResultDTO<UserDTO>> {
    const userId = route.paramMap.get('userId');
    if (userId != null) {
      return this.userService.getUser(Number(userId));
    }

    return empty();
  }
}
