import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { FormGroup } from '@angular/forms';
import { Location } from '@angular/common';
import { CarrierDTO } from '../../interfaces/carrier.dto';
import { CarrierUpdateDTO } from '../../interfaces/carrier-update.dto';
import { ServiceResultDTO } from '../../../../core/interfaces/service-result/service-result.dto';
import { CarrierService } from '../../services/carrier.service';
import { ResultHandlerService } from '../../../../core/services/result.handler.service';
import { getCarrierEditFormGroup } from '../../forms/carrier-edit-form-group';
import { SingleUploadComponent } from '../../../../shared/components/single-upload/single-upload.component';
import { RegionListComponent } from '../../../shared/region-list/region-list.component';
import { MaxFileSizes } from 'src/app/shared/constants/file-sizes';

@Component({
  selector: 'app-carrier-edit',
  templateUrl: './carrier-edit.component.html'
})
export class CarrierEditComponent implements OnInit {
  public carrier!: CarrierDTO;
  public form!: FormGroup;
  MaxFileSizes = MaxFileSizes;

  public carrierRegions!: number[];

  @ViewChild("upload")
  public upload!: SingleUploadComponent;

  @ViewChild("regions")
  public regionsComponent!: RegionListComponent;

  constructor(
    route: ActivatedRoute,
    private carrierService: CarrierService,
    private resultHandler: ResultHandlerService,
    private location: Location
  ) {
    const data = route.snapshot.data["carrier"] as ServiceResultDTO<CarrierDTO>;
    if (data.isSuccess) {
      this.carrier = data.value;
      this.carrierRegions = data.value.regions.map(r => r.id);
    } else {
      this.resultHandler.handle(data);
    }
  }

  ngOnInit(): void {
    this.initializeForm();
  }

  initializeForm() {
    this.form = getCarrierEditFormGroup(this.carrier)
  }

  return(): void {
    this.location.back();
  }

  onSubmit(): void {
    this.form.markAllAsTouched();
    if (!this.form.valid) return;

    const file = this.upload.getFile();
    const formValues = this.form.getRawValue();
    const carrierUpdate: CarrierUpdateDTO = {
      name: formValues.name.trim(),
      regionsIds: this.regionsComponent.getSelectedIds()
    }

    this.carrierService.validateUpdate(this.carrier.id, carrierUpdate, file).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) this.updateCarrier(carrierUpdate, file);
      });
    }, (err) => {
      this.resultHandler.handle(err.error).then((success) => {
        if (success) this.updateCarrier(carrierUpdate, file);
      });
    });
  }

  onUndo() {
    this.initializeForm();
    this.regionsComponent.reset();
    this.upload.reset();
  }

  public updateCarrier(carrierUpdate: CarrierUpdateDTO, file: File | undefined) : void{
    this.carrierService.updateCarrier(this.carrier.id, carrierUpdate, file).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) this.return();
      });
    }, (err) => {
      this.resultHandler.handle(err.error);
    });
  }

  get name() { return this.form.get('name'); }
}
