import { FormControl, FormGroup, Validators } from "@angular/forms";
import { FormValidators } from '@syncfusion/ej2-angular-inputs';
import { CarrierDTO } from "../interfaces/carrier.dto";

export function getCarrierEditFormGroup(carrier?: CarrierDTO): FormGroup {
  return new FormGroup({
    name: new FormControl(carrier ? carrier.name : '', [FormValidators.required])
  });
}
