import { Component, OnInit, QueryList, ViewChildren } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { RegionDTO } from '../../../region-management/interfaces/region.dto';
import { ServiceResultDTO } from '../../../../core/interfaces/service-result/service-result.dto';
import { ToolDTO } from '../../../../shared/interfaces/tools/tool.dto';
import { ToolManagementDTO } from '../../../../shared/interfaces/tools/tool-management.dto';
import { ToolManagementUpdateDTO } from '../../../../shared/interfaces/tools/tool-management-update.dto';
import { RegionService } from '../../../region-management/services/region.service';
import { ResultHandlerService } from '../../../../core/services/result.handler.service';
import { ToolService } from '../../services/tool.service';
import { ToolEditStateCarrierPartialComponent } from '../tool-edit-state-carrier-partial/tool-edit-state-carrier-partial.component';
import { NavService } from 'src/app/core/services/nav.service';

@Component({
  selector: 'app-tool-edit-state-carrier',
  templateUrl: './tool-edit-state-carrier.component.html',
  styleUrls: ['./tool-edit-state-carrier.component.scss']
})
export class ToolEditStateCarrierComponent implements OnInit {
  public tool!: ToolDTO;
  public regions!: RegionDTO[];
  public selectedRegionsIds: number[] = [];

  @ViewChildren("stateCarrier")
  stateCarrierComponents!: QueryList<ToolEditStateCarrierPartialComponent>;

  constructor(
    route: ActivatedRoute,
    private resultHandler: ResultHandlerService,
    private regionService: RegionService,
    private toolService: ToolService,
    navService: NavService
  ) {
    const data = route.snapshot.data["tool"] as ServiceResultDTO<ToolDTO>;
    if (data.isSuccess) {
      this.tool = data.value;
      this.selectedRegionsIds = data.value.regionsIds;
      navService.setToolNavMenu(this.tool);
    } else {
      this.resultHandler.handle(data);
    }
  }

  ngOnInit(): void {
    this.regionService.getActiveRegions().subscribe((data) => {
      if (data.isSuccess) {
        this.regions = data.value;
      } else {
        this.resultHandler.handle(data);
      }
    })
  }

  isRegionEnabled = (regionId: number): boolean => this.selectedRegionsIds.includes(regionId);

  getToolManagement(regionId: number): ToolManagementDTO | undefined {
    var toolManagement = this.tool.toolsManagement.filter(t => t.region.id == regionId);
    return toolManagement != null && toolManagement.length ? toolManagement[0] : undefined;
  }

  onUndo = () => this.stateCarrierComponents.forEach(a => { a.reset(); });

  public onSaveTool(): void {
    const model: ToolManagementUpdateDTO[] = [];
    this.stateCarrierComponents.forEach(a => {
      if (a.getModel() != null) model.push(a.getModel()!);
    });

    this.toolService.validateUpdateToolManagement(this.tool.id, model).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) this.update(model);
      });
    }, (err) => {
      this.resultHandler.handle(err.error).then((success) => {
        if (success) this.update(model);
      });
    });
  }

  public update(toolManagement: ToolManagementUpdateDTO[]): void {
    this.toolService.updateToolManagement(this.tool.id, toolManagement).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) this.tool = data.value;
      });
    }, (err) => this.resultHandler.handle(err.error));
  }
}
