import { FormControl, FormGroup, Validators } from "@angular/forms";
import { LogFilterDTO } from "../interfaces/log-filter.dto";

export function getLogsFormGroup(_filter?: LogFilterDTO): FormGroup {
  return new FormGroup({
    startDate: new FormControl(_filter ? _filter.startDate : null, [
      Validators.required
    ]),
    endDate: new FormControl(_filter ? _filter.endDate : null, [
      Validators.required
    ]),
    filterPattern: new FormControl(_filter ? _filter.filterPattern : '', [
      Validators.maxLength(120)
    ])
  });
}
