import { Component, ViewChild } from '@angular/core';
import { FilterSettingsModel, GridComponent, PageSettingsModel, SearchSettingsModel } from '@syncfusion/ej2-angular-grids';
import { TextBoxComponent } from '@syncfusion/ej2-angular-inputs';
import { AccountService } from '../../../authentication/services/account.service';
import { RestrictedComponent } from '../restricted/restricted.component';

@Component({
  template: ''
})
export class BaseGridComponent extends RestrictedComponent {
  public showArchived: boolean = false;

  public pageSettings!: PageSettingsModel;
  public filterSettings!: FilterSettingsModel;
  public searchSettings!: SearchSettingsModel;
  public allowPaging: boolean = false;
  private pageSizes!: number[];

  @ViewChild('grid')
  public grid!: GridComponent;
  @ViewChild('searchTextbox')
  public searchTextbox!: TextBoxComponent;

  constructor(
    accountService?: AccountService
  ) {
    super(accountService);
  }

  onCreate(args: any): void {
    this.searchTextbox.addIcon("append", "e-icons e-search");
  }

  search(event: any): void {
    if (event.value == null || event.value === '') {
      this.grid.clearFiltering();
      this.grid.search('');
    } else {
      this.grid.search(event.value);
    }
  };

  setupPagination(data: any[]): void {
    this.pageSizes = [10, 20, 50];
    this.pageSettings = { pageSize: this.pageSizes[0], pageSizes: this.pageSizes };
    if (data != null && data.length > this.pageSizes[this.pageSizes.length - 1]) {
      this.pageSizes.push(data.length);
    }

    this.allowPaging = true;
  }
}
